import {Component} from '../Component';
import {getOrDefault} from '../helpers/Misc';

class NTP extends Component
{
    /*
    * The NTP class provides methods to control time synchronization related operations.
    *
    * @example
    *    //TODO
    *
    * @class NTP
    * @constructor
    */
    constructor(params)
    {
        super(params);

        this._startTime = getOrDefault(params, "startTime",0);
        this._timeScale = getOrDefault(params, "scale",1);
        this._smoothingFactor = getOrDefault(params, "smoothingFactor", 0.9);
        this._NTPEnabled = getOrDefault(params, "NtpEnabled", false);

        this._currentTime = 0;
        this._deltaTime = 0;
        this._timeOffsetSmooth = 0;
        this._lastTime = new Date().getTime();

        this._offset = 0;
    }

    /**
    * Setup for this time instance
    *
    * @method setup
    */
    setup()
    {
        this._lastTime = new Date().getTime();

        this._offset = this._lastTime - this._startTime;

        if (this._NTPEnabled)
        {
            var socket = new WebSocket('ws://ntp.surexposition.mobi:8000/');
            ntp_websockets.init(socket);//how to import this vendor ?
            //debug.log("WebSocket init");
            setInterval(() => {
                this._offset = ntp_websockets.offset();
            }, 1000);
        }
    }


    start()
    {
        this._startTime = new Date().getTime();
    }

    pause()
    {

    }

    reset()
    {
        this._startTime = 0;
    }


    /**
    * updates this time instance
    *
    * @method update
    */
    update()
    {
        if(this.getStatus())//the component must be on, else we "stop" the time
        {
            let currentTime = new Date().getTime();
            this._deltaTime = (currentTime - this._currentTime)/1000;
            this._currentTime = currentTime;
        }
    }

    /**
    * Set the current timeScale, useful to slower time down
    *
    * @method setTimeScale
    * @param {Number} scale
    */
    setTimeScale(scale)
    {
        this._timeScale = scale;
    }

    /**
    * Get the current timeScale
    *
    * @method getTimeScale
    * @return {Number} scale
    */
    getTimeScale()
    {
        return this._timeScale;
    }

    /**
    * Get the current timeDelta (difference of time between 2 frames) at the current timeScale
    *
    * @method getDelta
    * @return {Number} the current timeDelta at the current timeScale
    */
    getDelta()
    {
        return this._deltaTime * this._timeScale;
    }

    /**
    * Gets the current Time object's time
    *
    * @method getTime
    * @return {Number} the current time
    */
    getTime()
    {
        return (this._currentTime + this._offset) * this._timeScale;
    }

    /**
    * Gets a smoothed time, useful to avoid hard transitions when time passed faster than the computing time.
    *
    * @method getTimeSmooth
    * @return {Number} the current time smoothed with an offset
    */
    getTimeSmooth()
    {
        this._timeOffsetSmooth = this._timeOffsetSmooth * this._startTime + this._offset * (1-this._startTime);
        return (this._currentTime + this._timeOffsetSmooth) * this._timeScale;
    }

    /**
    * Gets the current Time object underlying Date Object time
    *
    * @method getDateTime
    * @return {Number} the current Date Object raw getTime() result
    */
    getDateTime()
    {
        return new Date().getTime();
    }

    /**
    * Resync the current Time object with the given ntp server
    *
    * @method resync
    */
    resync()
    {
        if (this._NTPEnabled)
        {
            ntp_websockets.resync();
        }
    }
}

// module exports
export { NTP };
