import * as THREE from 'three';

import {_DOM} from '../helpers/DOM';
import {Context} from '../Context';

/*
 * @class EasyContext
 * @extends Context
 */
class EasyContext extends Context
{
    /*
    * Initiate the context by instanciating objects needed for the Mobilizing lib to work well! That is :
    *
    *     static Time Object
    *     Scenes array (context.scenes)
    *     Cameras array (context.cameras)
    *     Engine object
    *     device object (context.device)
    *     Canvas for rendering (context.canvas)
    *     Input object (context.input)
    *
    * @method init
    * @param {Canvas} canvas The canvas to use as a rendering surface
    * @param {JSON object} params inputDisabled to disable input
    */
    init(params = {})
    {
        super.init(params);

        params.context = this;

        this.audio      = this.addComponent(new Mobilizing.RendererAudio(params));
        this.renderer   = this.addComponent(new Mobilizing.Renderer3D(params));
        this.physics    = this.addComponent(new Mobilizing.PhysicsEngine(params));
        this.time       = this.addComponent(new Mobilizing.Time(params));
        this.touch      = this.addComponent(new Mobilizing.input.Touch({context:this}));
    }

    /*
    * Update all the context object that needs it (Time, Animation, Input)
    * @method update
    * @param {Canvas} canvas
    */
    update(canvas)
    {
        super.update(canvas);
    }

    getMainRenderer()
    {
        return this.renderer;
    }


    getRenderingSurface()
    {
        return this.canvas;
    }

    getTouchSurface()
    {
        return this.canvas;
    }

    /*
    * Tells the context to use this scene (defined by a string) or create it and switch to it.
    * @method setCurrentScene
    * @param {String} name
    */
    setCurrentScene(name)
    {
        this.renderer.setCurrentScene(name);
    }

    /*
    * Adds on object to the current scene
    * @method addToCurrentScene
    * @param {Object} object A Mesh or Light to add the ce scÃ¨ne
    */
    addToCurrentScene(object)
    {
        this.renderer.addToCurrentScene(object);
    }


    /*
    * Remove from the current scene
    * @method removeFromCurrentScene
    * @param {Object} object the object to remove
    */
    removeFromCurrentScene(object)
    {

        this.renderer.removeFromCurrentScene(object);
    }

    clear()
    {
        this.renderer.clear();
    }

    /*
    * Adds a camera to the current context
    * @method addCamera
    * @param {Camera} cam the camera to add
    */
    addCamera(cam)
    {
        this.renderer.addCamera(cam);
    }

    /*
    * Removes the camera from the current context
    * @method removeCamera
    * @param {Camera} cam
    */
    removeCamera(cam)
    {
        this.renderer.removeCamera(cam);
    }

    /*
    * Returns the current canvas'size as {width, height}
    * @method getCanvasSize
    * @return {Object} the size of the canvas as {width, height}
    */
    getCanvasSize(){
        return { width: this.canvas.width/window.devicePixelRatio,
                height: this.canvas.height/window.devicePixelRatio};
    }



    //move to RendererAudio
    beep(frequency)
    {
        this.audio.beep(frequency);
    }


    // Helper function to get an element's exact position
    getCanvasPosition(){
        return _DOM.getElementPosition(this.canvas);
    }

    updateCanvasPosition() {
        this.canvasPosition = this.getCanvasPosition();
    }





    /*
     * Creates a Mobilizing Cube
     * @method createCube
     * @return {Object} a Mobilizing cube
    */
    createCube()
    {
        //var cube = new Mobilizing.Cube(...arguments);
        var cube = new Mobilizing.Mesh({primitive:"box"});
        this.addToCurrentScene(cube);
        return cube;
    }

    /*
     * Creates a Mobilizing sphere
     * @method createSphere
     * @return {Object} a Mobilizing sphere
     */
    createSphere()
    {
        //var sphere = new Mobilizing.Sphere(...arguments);
        var sphere = new Mobilizing.Mesh({primitive:"sphere"});
        //var sphere = Mobilizing.Mesh.CreateSphere(1,100,100);
        this.addToCurrentScene(sphere);
        return sphere;
    }


    /*
     * Creates a Mobilizing Plane
     * @method createPlane
     * @return {Object} a Mobilizing Plane
     */
    createPlane(/*width, height*/)
    {
        //var plane = Mobilizing.Mesh.CreatePlane(width, height);
        var plane = new Mobilizing.Mesh({primitive:"plane"});
        this.addToCurrentScene(plane);
        return plane;
    }

    /*
     * Creates a Mobilizing Light
     * @method createLight
     * @return {Object} a Mobilizing light
     */
    createLight()
    {
        var light = new Mobilizing.Light(...arguments);
        this.addToCurrentScene(light);
        return light;
    }

    /*
     * Creates a Mobilizing label
     * @method createLabel
     * @return {Object} a Mobilizing label
     */
    createLabel(params)
    {
        params.context = this;
        var label = new Mobilizing.Label(params);
        this.addToCurrentScene(label);
        return label;
    }

    /*
     * Creates a Mobilizing label
     * @method createLabel
     * @return {Object} a Mobilizing label
     */
    createMesh(params)
    {
        params.context = this;
        var mesh = new Mobilizing.Mesh(params);
        this.addToCurrentScene(mesh);
        return mesh;
    }

    /*
     * Creates a Mobilizing Camera
     * @method createCamera
     * @return {Object} a Mobilizing camera
     */
    createCamera()
    {
        var cam = new Mobilizing.Camera(...arguments);
        this.addCamera(cam);
        this.addToCurrentScene(cam);
        return cam;
    }


    /*
     * Creates a Mobilizing Camera
     * @method createCameraX
     * @param {Object} params A multi purpose parametrization object
     * @return {Object} a Mobilizing camera
     */
    createCameraX(params)
    {
        //DEPRECATED
        var cam = new Mobilizing.Camera(params.type);
        this.addCamera(cam);
        this.addToCurrentScene(cam);

        if (params.fov !== undefined)
        {
            cam.setFOV(params.fov);
        }
        if (params.position !== undefined)
        {
            cam.transform.setLocalPosition(params.position);
        }
        if (params.layer !== undefined)
        {
            cam.layer = params.layer;
        }

        return cam;
    }




    /*
     * Creates a Text Label
     * @method createTextLabel
     * @return {Object} a Mobilizing Label
     */
    createTextLabel(text, font, fontSize, width, height, color, backgroundColor, texture/*, hasBackground*/)
    {
        var labelTest = new Mobilizing.Label(text, font, width, height);
        labelTest.style.fontSize    = fontSize;
        labelTest.style.color       = color;
        if (backgroundColor !== undefined)
        {
            labelTest.style.hasBackground = true;
            labelTest.style.backgroundColor = backgroundColor;
        }
        else
        {
            labelTest.style.hasBackground = false;
        }
        //labelTest.style.textAlign = align;//"center|end|left|right|start"
        labelTest.style.textAlign = "center";//"center|end|left|right|start"
        if (texture !== undefined)
        {
            labelTest.material.texture = texture;
            labelTest.transform.setLocalScale(width,height,1);
        }
        else
        {
            labelTest.render();
        }

        this.addToCurrentScene(labelTest);
        return labelTest;
    }


    /*
     * Creates a Mobilizing Video Quad
     * @method createVideoQuad
     * @return {Object} a Mobilizing Video Quad
     */
    createVideoQuad(url)
    {
        var quad = new Mobilizing.Mesh({primitive:"plane"});

        quad.vt = new Mobilizing.VideoTexture();
        quad.vt.loadFromFile(url);
        quad.material.setTexture(quad.vt);
        this.addToCurrentScene(quad);
        return quad;
    }

    /*
     * Loads a Mobilizing Font
     * @method loadFont
     * @return {Object} a Mobilizing Font
     */
    loadFont(url)
    {
        var font = new Mobilizing.Font(url, this);
        return font;
    }


    loadObj3dModel (model_url, texture_url)
    {
        //load model from OBJ file
        var model = new Mobilizing.Mesh();
        model.material = new Mobilizing.Material({type:"lambert"});
        model.updateMaterial();
        model.loadFromFile(model_url, this.onObjLoaded.bind(this));
        //load texture
        var tex = new Mobilizing.Texture({context:this});
        tex.loadFromFile(texture_url);
        //apply texture to model
        model.material.setTexture(tex);
        //mob.addToCurrentScene(model); //doing this before the completed loading may result in an no rendering, we have to do that in the callback given to loadFromFile()
        //console.log("added to current scene");
        return model;
    }

    onObjLoaded (model)
    {
        //we add the just loaded model to the 3D scene
        this.addToCurrentScene(model);
    }


    /*
     * Creates a Mobilizing Audio source
     * @method createAudioSource
     * @return {Object} a Mobilizing Audio source
     */
    createAudioSource(url)
    {
        var source  = new Mobilizing.AudioSource(this);
        source.set3D(false);
        var sound   = new Mobilizing.AudioBuffer(this);
        sound.loadFromURL(url, this.onBufferLoaded.bind(this));
        return source;
    }

    onBufferLoaded (/*source, buffer*/)
    {
        //source.setBuffer(sound);
        //source.play();
    }


    /*
     * Creates a Mobilizing Texture
     * @method createTexture
     * @return {Object} a Mobilizing texture
     */
    createTexture()
    {
        var texture = new Mobilizing.Texture({context:this});
        return texture;
    }

    /*
     * Creates a Mobilizing Texture
     * @method createTextureFromCubeCamera
     * @return {Object} a Mobilizing texture
     */
    createTextureFromCubeCamera(camera)
    {
        var texture = new Mobilizing.Texture({context:this});
        texture.cube = camera._camera.renderTarget; //FIXME
        texture.cube.minFilter = THREE.LinearMipMapLinearFilter;
        return texture;
    }


    /*
     * Loads a Mobilizing Texture
     * @method loadFont
     * @return {Object} a Mobilizing Texture
     */
    loadTexture(url, asynchro)
    {
        var texture = new Mobilizing.Texture({context:this});
        texture.loadFromFile(url, asynchro);
        return texture;
    }


    /*
     * Creates a Mobilizing Button
     * @method createTextButton
     * @return {Object} a Mobilizing Button
     */
    createTextButton(text, width, height, font, fontSize, position, scale)
    {
        var button = Mobilizing.Button.CreateButton(undefined, text, font, fontSize, position, width, height, undefined, undefined,this);
        button.transform.setLocalScale(scale);
        this.addToCurrentScene(button);
        return button;
    }
}

// module exports
export { EasyContext };
