import {Component} from '../Component';
import {singletonize} from '../helpers/Misc';

class RendererAudio extends Component
{
    /**
     * RendererAudio is a Web Audio API based renderer for playing sound.
     *
     * @example
     *     //to do
     *
     * @class RendererAudio
     * @constructor
     * @extends Component
     * @param {Object} params Config parameters
    */
    constructor(params)
    {
        super(params);

        this.renderer = this.getContext();
        this.audioContext = undefined;

        //web Audio API
        if (this.params === undefined || this.params.audioEnabled)
        {
            if (window.AudioContext !== undefined)
            {
                this.audioContext = new window.AudioContext();
            }
            else if (window.webkitAudioContext !== undefined)
            {
                this.audioContext = new window.webkitAudioContext();
            }

            //hack to reset the SR
            if (this.audioContext)
            {
                if (this.audioContext.sampleRate !== 44100)
                {
                    var buffer = this.audioContext.createBuffer(1, 1, 44100);
                    var dummy = this.audioContext.createBufferSource();
                    dummy.buffer = buffer;
                    dummy.connect(this.audioContext.destination);
                    dummy.start(0);
                    dummy.disconnect();
                    this.audioContext.close();

                    if (window.AudioContext !== undefined)
                    {
                        this.audioContext = new window.AudioContext();
                    }
                    else if (window.webkitAudioContext !== undefined)
                    {
                        this.audioContext = new window.webkitAudioContext();
                    }
                }

                this.audioOutputNode = this.audioContext.createGain();
                this.audioOutputNode.connect(this.audioContext.destination);
            }
        }

        this.listener = this.audioContext.listener;

        if(this.listener.forwardX)
        {
            this.listener.forwardX.value = 0;
            this.listener.forwardY.value = 0;
            this.listener.forwardZ.value = -1;
            this.listener.upX.value = 0;
            this.listener.upY.value = 1;
            this.listener.upZ.value = 0;
        }
        else
        {
            this.listener.setOrientation(0,0,-1,0,1,0);
        }

    }

    beep(frequency)
    {
        let freq = (frequency) ? frequency : 440;
        var osc = this.audioContext.createOscillator();
        //this.audioOutputNode.gain.value = 0;
        osc.connect(this.audioOutputNode);
        osc.frequency.value = freq;
        osc.start(0);
        osc.stop(this.audioContext.currentTime + 0.3);
    }

    setListenerTransform(transform)
    {
        this.listener.transform = transform;
    }

    /*setup()
    {
    }

    on()
    {
        super.on();
    }

    off()
    {
        super.off();
    }
    */
    update()
    {
        super.update();

        if(this.listener.transform)
        {
            var pos = this.listener.transform.getLocalPosition();
            this.listener.setPosition(pos.x, pos.y, pos.z);
        }
    }
}


const RendererAudioSingleton = singletonize(RendererAudio);

// module exports
export { RendererAudioSingleton as RendererAudio };
