import {Component} from '../Component';
import {getOrDefault, noop} from '../helpers/Misc';
import {Pointer} from "../helpers/Pointer";
import {Mouse} from "../input/Mouse";

class Clickable extends Component
{
    /**
    * Makes a Mesh clickable
    *
    * @class Clickable
    * @constructor
    * @param {Object} params parameters object
    * @param {Pointer} params.pointer
    * @param {Camera} params.camera
    * @param {onPress} params.onPress
    * @param {onRelease} params.onRelease
    * @param {onEnter} params.onEnter
    * @param {onLeave} params.onLeave
    */
    constructor(params)
    {
        super(params);

        this.pointer = getOrDefault(params, "pointer", undefined);
        this.onPress = getOrDefault(params, "onPress", noop);
        this.onRelease = getOrDefault(params, "onRelease", noop);
        this.onEnter = getOrDefault(params, "onEnter", noop);
        this.onLeave = getOrDefault(params, "onLeave", noop);

        this.camera = params.camera;
        this.target = getOrDefault(params, "target", undefined);

        this.picked = false;
        this.hovered = false;
    }

    /**
    * Setup for this clickable instance
    *
    * @method setup
    */
    setup()
    {
        super.setup();

        let context = this.getContext();

        if(!this.pointer)
        {
            let mouse = new Mouse();
            context.addComponent(mouse);
            mouse.setup();
            mouse.on();

            this.pointer = new Pointer();
            this.pointer.add(mouse);
            context.addComponent(this.pointer);
            this.pointer.setup();
            this.pointer.on();
            //console.log(mouse,context);
        }

        this.pointer.events.on("on", event => {

            let pick = this.target.transform.pick(this.camera, event.x, event.y);

            if(pick)
            {
                this.picked = true;
                this.onPress();
            }
            else
            {
                this.picked = false;
            }
        });

        this.pointer.events.on("off", (/*event*/) => {

            // let pick = this.target.transform.pick(this.camera, event.x, event.y);

            if(this.picked === true)
            {
                this.onRelease();
            }

        });

        this.pointer.events.on("moved",event => {

            let pick = this.target.transform.pick(this.camera, event.x, event.y);

            if(pick)
            {
                this.picked = true;
                if(!this.hovered)
                {
                    this.onEnter();
                    this.hovered = true;
                }
            }
            else
            {
                if(this.picked) //avoid useless recall
                {
                    this.onLeave();
                    this.hovered = false;
                    this.picked = false;
                }
            }
        });
    }

}

// module exports
export { Clickable };
