import {getOrDefault} from '../helpers/Misc';
import {debug} from '../helpers/Debug';
import {Component} from '../Component';
import {_Math} from '../helpers/Math';

/**
 * Fired when the location has been updated
 * @event locationupdated
 */
const EVT_LOC_UPDATED = "locationupdated";

/**
 * Fired when the location update generated an error
 * @event locationerror
 */
const EVT_LOC_ERROR = "locationerror";

class GPS extends Component
{
    /**
     * @class GPS
     * @constructor
     *
     * @param {Object} params Config parameters
     * @param {Object} params.context the Mobilizing context this Input instance is linked to
     * @param {Boolean} params.enableHighAccuracy Boolean to activate accurate location (eats more battery and takes time)
     * @param {Number} params.maximumAge Number of millisecond to define the maximum age of the geolocation data (default = 500000)
     * @param {Number} params.timeout timeout of the location service in millisecond
     */
    constructor(params)
    {
        super(params)

        /**
        * Accurate location (eats more battery and takes time)
        * @private
        * @property {Boolean} _enableHighAccuracy
        */
        this._enableHighAccuracy = getOrDefault(params, "enableHighAccuracy", false);

        /**
        * Number of millisecond to define the maximum age of the geolocation data
        * @private
        * @property {Number} _maximumAge
        */
        this._maximumAge = getOrDefault(params, "maximumAge", undefined);

        /**
        * Timeout of the location service in millisecond
        * @private
        * @property {Number} _timeout
        */
        this._timeout = getOrDefault(params, "timeout", undefined);

        /**
         * true if the GPS is available on the device, false otherwise
         * @private
         * @property {Boolean} _GPSAvailable
        */
        this._GPSAvailable = (navigator !== undefined) && ("geolocation" in navigator);
    }

    /**
    * Set the GPS up.
    * @method setup
    */
    setup()
    {
        /**
         * Object containing the informations given by the GPS. Used internally.
         * @property {Object} _location
         * @private
        */
        this._location = {
            latitude : null,
            longitude : null,
            altitude : null,
            accuracy: null,
            altitudeAccuracy : null,
            heading : null,
            speed : null,
            timestamp : null
        };

        // this._watchID;
    }

    /**
    * Activates the component
    * @method on
    */
    on()
    {
        if(this._GPSAvailable)
        {
            super.on();

            let positionOptions = {
                enableHighAccuracy: this._enableHighAccuracy,
                timeout: this._timeout,
                maximumAge: this._maximumAge
            };

            this._watchID = navigator.geolocation.watchPosition(
                event => this.updateLocationCallBack(event),
                event => this.updateLocationErrorCallBack(event),
                positionOptions
            );
        }
        else
        {
            debug.info("this device has no GPS!");
        }
    }

    /**
    * Deactivate the component
    * @method off
    */
    off()
    {
        super.off();

        navigator.geolocation.clearWatch(this._watchID);
    }

    /**
    * Enable HighAccuracy. Requieres to set the component off once then to set it on again to use the new value.
    * @method setHighAccuracy
    * @param {Boolean} val Enable or not high accuracy
    */
    setEnableHighAccuracy(val)
    {
        this._enableHighAccuracy = val;
    }

    /**
    * Set the time out for location update. Requieres to set the component off once then to set it again on to use the new value.
    * @method setTimeout
    * @param {Number} val the new value
    */
    setTimeout(val)
    {
        this._timeout = val;
    }

    /**
    * Set the maximum age of a location data. Requieres to set the component off once then to set it on again to use the new value.
    * @method setMaximumAge
    * @param {Number} val the new value
    */
    setMaximumAge(val)
    {
        this._maximumAge = val;
    }

    /**
      * returns the most recent location data. Location service MUST be activated on the device, or errors will get out from the callbacks!
      * The return objects contains the standard HTML5 properties for geolocation :
      *
      *     location.longitude
      *     location.latitude
      *     location.altitude
      *     location.accuracy
      *     location.altitudeAccuracy
      *     location.heading
      *     location.speed
      *     location.timestamp
      *
      * @method getLocation
      * @return {Object} the location object updated with the most recent data.
      */
    getLocation()
    {
        if (this._active) {
            return this._location;
        }

        debug.warn("geolocation is off, turn it on with context.input.setGeolocationStatus(true);");

        return null;
    }

    /**
     * Listener for the update of the location object with the most recent data.
     *
     * @method updateLocationCallBack
     * @param {Object} position the HTML5 position object to use for the Mobilizing location object's update
     * @private
    */
    updateLocationCallBack(position)
    {
        //debug.log(position);
        this._location.longitude = position.coords.longitude;
        this._location.latitude = position.coords.latitude;
        this._location.altitude = position.coords.altitude;
        this._location.accuracy = position.coords.accuracy;
        this._location.altitudeAccuracy = position.coords.altitudeAccuracy;
        this._location.heading = position.coords.heading;
        this._location.speed = position.coords.speed;
        this._location.timestamp = position.timestamp;
        this._location.error = "no error";

        this.events.trigger(EVT_LOC_UPDATED, this._location);
    }

    /**
     * Listener for the update errors of the location object with the most recent data.
     *
     * @method updateLocationErrorCallBack
     * @param error
     * @private
    */
    updateLocationErrorCallBack(error)
    {
        if(error.code === 1){
            console.error("location permission denied");
            this._location.error = "location permission denied";

        }else if(error.code === 2){
            console.error("location unavailable");
            this._location.error = "location unavailable";

        }else if(error.code === 3){
            console.error("location timeout");
            this._location.error = "location timeout";
        }

        this.events.trigger(EVT_LOC_ERROR, this._location.error);
    }

    /**
     * reset the location object to avoid bugs
     * @private
     * @deprecated
     * @method resetLocation
     */
    resetLocation()
    {
        this._location = {
            latitude : null,
            longitude : null,
            altitude : null,
            accuracy: null,
            altitudeAccuracy : null,
            heading : null,
            speed : null,
            timestamp : null
        };
    }
}

var _STATICS = {};
_STATICS.zoom = 1;
_STATICS.worldSize = 512 * (Math.pow(2, _STATICS.zoom));

/**
* Some GPS coordinates convertion tools to use maps in Mobilizing.js
* @class GPSUtils
* @constructor
* @static
*/
class GPSUtils
{

    /**
    * Converts longitude and latitude coords (from an OpenStreetMap map) into absolute pixels coords. This method don't calculate the pixels coords within a given map bound's! Use getCoordinateInMap for this.
    * @static
    * @method getPixelsCoordsFromOSMLngLat
    * @param {Number} longitute longitute
    * @param {Number} latitude latitude
    * @param {Number} zoom the zoom factor fo this map
    */
    static getPixelsCoordsFromOSMLngLat(longitute, latitude, zoom)
    {
        if(zoom)
        {
            _STATICS.zoom = zoom;
            _STATICS.worldSize = 512 * (Math.pow(2, _STATICS.zoom));
        }

        return {x: GPSUtils.lngX(longitute), y: GPSUtils.latY(latitude)};
    }

    /**
    * Compute GPS coordinates into pixels x, y coordinates in a map. Here, we adopt the OSM standard for mappinp. Therefore, you should generate your bitmap earth map with a tool using OSM tiles (like MapBox, TileMill). You should give the zoom factor and the map bounds (in lognititude and latitude) to this method for the calculation to be effective
    * @static
    * @method getCoordinateInMap
    * @param {Number} longitute
    * @param {Number} latitude
    * @param {Number} zoom zoom factor fo this map
    * @param {Array} mapCoord MapBox/TileMill map coords array, like [-180,-85.0511,180,85.0511]
    * @param {Number} mapWidth the map width in pixels
    * @param {Number} mapHeight the map height in pixels
    */
    static getCoordinateInMap(longitute, latitude, mapCoord, mapWidth, mapHeight, zoom)
    {
        if(zoom)
        {
            _STATICS.zoom = zoom;
        }
        //construct local variable to simplify expressions writing
        let leftLong = mapCoord[0];
        let bottomLat = mapCoord[1];

        let rightLong = mapCoord[2];
        let topLat = mapCoord[3];

        //construct the corners pixel coordinates
        let topRight = GPSUtils.getPixelsCoordsFromOSMLngLat(rightLong, topLat);
        let topLeft = GPSUtils.getPixelsCoordsFromOSMLngLat(leftLong, topLat);

        let bottomRight = GPSUtils.getPixelsCoordsFromOSMLngLat(rightLong, bottomLat);
        // let bottomLeft = GPSUtils.getPixelsCoordsFromOSMLngLat(leftLong, bottomLat);

        let absCoord = GPSUtils.getPixelsCoordsFromOSMLngLat(longitute, latitude);

        let coords = {x: _Math.map(absCoord.x, topRight.x, topLeft.x, mapWidth/2, -mapHeight/2),
                      y: _Math.map(absCoord.y, topRight.y, bottomRight.y, mapWidth/2, -mapHeight/2)};

        return coords;
    }

    /**
     * latitude to absolute x coord
     * @static
     * @method lngX
     * @param {Number} lon
     * @return {Number} pixel coordinate
     */
    static lngX(lng) {
        return (180 + lng) * _STATICS.worldSize / 360;
    }
    /**
     * latitude to absolute y coord
     * @static
     * @method latY
     * @param {Number} lat
     * @return {Number} pixel coordinate
     */
    static latY(lat) {
        const y = 180 / Math.PI * Math.log(Math.tan(Math.PI / 4 + lat * Math.PI / 360));
        return (180 - y) * _STATICS.worldSize / 360;
    }

    static xLng(x) {
        return x * 360 / _STATICS.worldSize - 180;
    }
    static yLat(y) {
        const y2 = 180 - y * 360 / _STATICS.worldSize;
        return 360 / Math.PI * Math.atan(Math.exp(y2 * Math.PI / 180)) - 90;
    }

    /**
    * Convert GPS coords to cartesian coords. NB : this algorithm is adapted to three.js geoms!
    * @static
    * @property getPolarFromLngLat
    * @param {Numbe} longitude
    * @param {Numbe} latitude
    * @param {Numbe} radius
    * @return {Object} {x, y, z} coordinates
    */
    static getPolarFromLngLat(longitude, latitude, radius)
    {
        let lng = _Math.degToRad(longitude + 180);
        let lat = _Math.degToRad(90 - latitude);

        let x = -(radius * Math.cos(lng) * Math.sin(lat));
        let y = radius * Math.cos(lat);
        let z = radius * Math.sin(lat) * Math.sin(lng);

        return {x: x, y: y, z: z};
    }

}

// module exports
export { GPS, GPSUtils };
