import * as THREE from 'three';

/**
 * Matrix3 class
 *
 * This class extends the one from Three.js, API available here : http://threejs.org/docs/#Reference/Math/Matrix3
 * @class Matrix3
**/

class Matrix3 extends THREE.Matrix3
{
}

/**
@method transpose
@return Matrix3
*/

/**
 Transposes this matrix into the supplied array, and returns itself unchanged.
@method transposeIntoArray
@return Matrix3
@param array {Array}
*/

/**
@method determinant
@return Float
*/

/**
 Sets the 3x3 matrix values to the given row-major sequence of values.
@method set
@return Matrix3
@param n11 {Float}
@param n12 {Float}
@param n13 {Float}
@param n21 {Float}
@param n22 {Float}
@param n23 {Float}
@param n31 {Float}
@param n32 {Float}
@param n33 {Float}
*/

/**
 Multiplies every component of the matrix by the scalar value *s*.
@method multiplyScalar
@return Matrix3
@param s {Float}
*/

/**
 Multiplies (applies) this matrix to every vector3 in the array.
@method applyToVector3Array
@return Array
@param array {Array}
*/

/**
 Sets this matrix as the normal matrix (upper left 3x3)of the passed Matrix4 matrix4. The normal matrix is the inverse transpose of the matrix *m*.
@method getNormalMatrix
@return Matrix3
@param m {Matrix4}
*/

/**
 Set this matrix to the inverse of the passed matrix.
@method getInverse
@return Matrix3
@param m {Matrix4}
@param throwOnDegenerate {Boolean}
*/

/**
 Copies the values of matrix *m* into this matrix.
@method copy
@return Matrix3
@param m {Matrix3}
*/

/**
@method clone
@return Matrix3
*/

// module exports
export { Matrix3 };
