
/**
 * Method to retreive a value from an object if available, or a default value
 * Used in class constructors
 *
 * @method getOrDefault
 * @param {Object} object The object to retreive the value from if available
 * @param {String} key The object's key from which the value should be looked up
 * @param {Mixed} defaultValue The default value to return if the object does not contain the given key, or if the value accociated with the key is undefined
 * @return {Mixed} The retreived a value or the default one
 */
function getOrDefault(object, key, defaultValue) {
    return ((object !== undefined) && (key in object)) ? object[key] : defaultValue;
}

/**
 * Method to retreive a value from an object if available, or the return value of a function with optional arguments
 * Used in class constructors
 *
 * @method getOrExec
 * @param {Object} object The object to retreive the value from if available
 * @param {String} key The object's key from which the value should be looked up
 * @param {Function} fn The function to call if no value was found
 * @param {Mixed} [...args] Optional arguments to send with the function
 * @return {Mixed} The retreived a value or the function's return one
*/
function getOrExec(object, key, fn, ...args) {
    return ((object !== undefined) && (key in object)) ? object[key] : fn(...args);
}

/**
 * Method to retreive a value from an object if available, or instantiate a class with optional arguments
 * Used in class constructors
 *
 * @method getOrCreate
 * @param {Object} object The object to retreive the value from if available
 * @param {String} key The object's key from which the value should be looked up
 * @param {Class} cls The class to instantiate if no value was found
 * @param {Mixed} [...args] Optional arguments to send to the class's constructor
 * @return {Mixed} The retreived a value or the new class instance
*/
function getOrCreate(object, key, cls, ...args) {
    return ((object !== undefined) && (key in object)) ? object[key] : new cls(...args);
}

/**
 * Method to force a class to have only one instance
 *
 * @method singletonize
 * @param {class} def The definition of the class
 */
function singletonize(def){

    let instance;

    return function(...args){

        if(instance === undefined){
            instance = new def(...args);
        }
        return instance;
    }
}

/**
 * Method to convert a string to Base64. Used internally to convert loaded raw data to img. Wrote by  phil@philten.com - http://www.philten.com
 * @private
 * @method encode64
 * @param {String} inputStr
 * @author phil@philten.com - http://www.philten.com
 * Date: 2010/12/15
 * version: 1.0
 * url french: http://www.philten.com/fr-xmlhttprequest-image
 * url english: http://www.philten.com/us-xmlhttprequest-image
 */
function encode64(inputStr)
{
    var b64 = "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/=";
    var outputStr = "";
    var i = 0;

    while (i<inputStr.length)
    {
        //all three "& 0xff" added below are there to fix a known bug
        //with bytes returned by xhr.responseText
        var byte1 = inputStr.charCodeAt(i++) & 0xff;
        var byte2 = inputStr.charCodeAt(i++) & 0xff;
        var byte3 = inputStr.charCodeAt(i++) & 0xff;

        var enc1 = byte1 >> 2;
        var enc2 = ((byte1 & 3) << 4) | (byte2 >> 4);

        var enc3, enc4;
        if (isNaN(byte2))
        {
            enc3 = enc4 = 64;
        }
        else
        {
            enc3 = ((byte2 & 15) << 2) | (byte3 >> 6);
            if (isNaN(byte3))
            {
                enc4 = 64;
            }
            else
            {
                enc4 = byte3 & 63;
            }
        }

        outputStr +=  b64.charAt(enc1) + b64.charAt(enc2) + b64.charAt(enc3) + b64.charAt(enc4);
    }

    return outputStr;
}

/**
 * Converts a DOMString to an ArrayBuffer (raw data binary).
 *
 * @method stringToArrayBuffer
 * @param {DOMString} str a string returned by a XMLHTTPRequest (responseText)
 * @return {ArrayBuffer} the resulting ArrayBuffer
 */
function stringToArrayBuffer(str){
    //var s = this.encode_utf8(str);
    //var buf = new ArrayBuffer(s.length);
    var buffer = new ArrayBuffer(str.length);
    var bufView = new Uint8Array(buffer);
    //for (var i=0, strLen=str.length; i<strLen; i++) {
    for (var i=0, strLen=str.length; i<strLen; i++) {
        //bufView[i] = s.charCodeAt(i);
        bufView[i] = str.charCodeAt(i);
    }
    return buffer;
}

/**
 * Extract the MimeType from the URL string of image files. Used internally for texture loading
 * @private
 * @method getMimeType
 * @param {String} url
 * @param {String} mimeType from the url
 */
function getMimeType(url){

    var mimeType = null;

    var extension = (function(filename){

        if (filename.lastIndexOf(".") > 0) {
            return filename.substr(filename.lastIndexOf(".")).toLowerCase();
        }

        return null;

    })(url);

    if( extension === ".jpg" || extension === ".jpeg"){
        mimeType = "jpeg";
    }else if( extension === ".tiff" || extension === ".tif"){
        mimeType = "tiff";
    }else if(extension === ".png"){
        mimeType = "png";
    }else if(extension === ".gif"){
        mimeType = "gif";
    }

    console.log("extension " + extension, "mimeType " + mimeType);

    return mimeType;
}

function noop() {
    return null;
}

// module exports
export { getOrDefault, getOrExec, getOrCreate, singletonize, encode64, stringToArrayBuffer, getMimeType, noop };
