import * as THREE from 'three';

import {getOrDefault} from '../helpers/Misc';
import {Device} from '../helpers/Device';
import {CanvasTexture} from './CanvasTexture';
import {debug} from '../helpers/Debug';

class VideoTexture
{
    /**
     * VideoTexture Class give the possibility to use a movie file directly as a texture. Therefore, this class contains some movie media fonctions to manage time (play, pause, stop, etc). The texture must be mapped on an Mesh via a Material (setTexture()).
     *
     * @class VideoTexture
     * @constructor
     * @param {Object} params params The parameters object
     * @param {Video} params.video the html5 video object to use for this VideoTexture
     * @param {Number} [params.fps = 30] the fps to use for this video playback, used only with iOS devices. NB : this parameter will not change the playback speed of the video, it will only affect it's picture update frequency.
     * @param {Boolean} [params.loop = false] whether the video should loop after playback end
     * @param {Boolean} [params.autoPlay = false] whether the video should play at construction
     *
     * @example
     *    //TODO
     */
    constructor(params)
    {
        this._video = getOrDefault(params, "video", undefined);
        this._loop = getOrDefault(params, "loop", false);
        this._autoPlay = getOrDefault(params, "autoPlay", false);
        this._fps = getOrDefault(params, "fps", 30);

        //special treatment for iOS as we can't access the video without an automatic fullscreen
        this._isIOS = (Device.getOS() === "iOS");

        this._isPlaying = false;
        this._bufferedPourcent = {start:0,end:0};

        //iOS specifics, we must create a canvas to render in or we'll go fullscreen
        if(this._isIOS)
        {
            this._autoPlay = false;//no autoplay on iOS
            this._videoCanvasTexture = new CanvasTexture({width:this._video.videoWidth,
                                                          height:this._video.videoHeight});
        }
        //to have some events to work with
        this._video.addEventListener("progress", this.onLoadProgess.bind(this));
        this._video.addEventListener("ended", this.onVideoEnded.bind(this));

        if(!this._isIOS)
        {
            this._texture = new THREE.Texture( this._video );
            this._texture.generateMipmaps = false;
            this._texture.minFilter = THREE.NearestFilter;
            this._texture.magFilter = THREE.NearestFilter;
        }
        else
        {
            this._texture = this._videoCanvasTexture._texture;
        }

        if(this._autoPlay && !this._isIOS)
        {
            this.play();
        }
    }

    /**
    * Defines a "load progress" behavior. User can access the result of the progress through the bufferedPourcent property (videoTexture.bufferedPourcent) which is an object constructed like this : this._bufferedPourcent = {start:<Number>, end: <Number>}. FIXME : should map to the orginal list produced by HTML5 TimeRange object
    * @private
    * @method onVideoSrcLoaded
    * @param {Event} e
    */
    onLoadProgess()
    {
        var timeRange = this.getBuffered();
        debug.log("progress",timeRange,this.getDuration());

        if(timeRange)
        {
            if(timeRange.length >= 1)
            {
                this._bufferedPourcent = {start:this.getBuffered().start(0) * 100 / this.getDuration(),
                                          end:this.getBuffered().end(0)* 100 / this.getDuration()};
            }
        }
    }

    /**
    * Ended event to manage loop playback
    * @private
    * @method onVideoEnded
    * @param {Event} e
    */
    onVideoEnded()
    {
        if(this._loop)
        {
            this.play();
        }
        else
        {
            this.stop();
        }
    }

    /**
    * Set Loop on or off for this video
    * @method setLoop
    * @param {Boolean} val
    */
    setLoop(val)
    {
        this._loop = val;
    }

    /**
    * updates the video texture, should be done everytime an update is desired (i.e. every frame)
    * @method update
    */
    update()
    {
        //if ( this._video.readyState === this._video.HAVE_ENOUGH_DATA || this._video.readyState === this._video.HAVE_METADATA )
        if (this._video.readyState >= this._video.HAVE_FUTURE_DATA)
        {
            if(this._isIOS && this._isPlaying){

                var time = Date.now();
                var elapsed = (time - this.lastTime) / 1000;

                if(elapsed >= (1 / this._fps) )
                {

                    this._video.currentTime = this._video.currentTime + elapsed;
                    this.lastTime = time;

                    this._videoCanvasTexture.drawImage(this._video);
                    // Resync audio and video if they drift more than 300ms apart
                    /* if(this.audio && Math.abs(this.audio.currentTime - this._video.currentTime) > 0.3){
                        this.audio.currentTime = this._video.currentTime;
                    }*/
                }
            }

            if (!this._isIOS && this._texture)
            {
                this._texture.needsUpdate = true;
            }
        }
    }

    /**
    * Play the video
    * @method play
    */
    play()
    {
        if(!this._isIOS)
        {
            this._video.play();
        }
        else
        {
            this.lastTime = Date.now();
        }
        this._isPlaying = true;
    }

    /**
    * Pause the video
    * @method pause
    */
    pause()
    {
        this._video.pause();
        this._isPlaying = false;
    }

    /**
    * Stop the video
    * @method stop
    */
    stop()
    {
        this._video.pause();
        this._video.currentTime = 0;
        this._isPlaying = false;
    }

    /**
    * Get playing state
    * @method getIsPlaying
    * @return {Boolean} playing state
    */
    getIsPlaying()
    {
        return this._isPlaying;
    }

    /**
    * Gets the current time of this video
    * @method getCurrentTime
    * @return {Number} the current time of this video in seconds
    */
    getCurrentTime()
    {
        return this._video.currentTime;
    }
    /**
    * Gets the current buffered part of this video
    * @method getBuffered
    * @return {Object} the current buffered part as a TimeRange Object {length:Number, start:Number, end:Number}
    */
    getBuffered()
    {
        return this._video.buffered;
    }

    /**
    * Set the current time of this video
    * @method setCurrentTime
    * @param {Number} t the current time (in second) to
    */
    setCurrentTime(t)
    {
        if(this._video.currentTime < this._video.duration){
            this._video.currentTime = t;
        }else{
            this._video.currentTime = this._video.duration;
        }
    }

    /**
    * Get the duration of the video file in seconds
    * @method getDuration
    * @return {Number} duration
    */
    getDuration()
    {
        return this._video.duration;
    }

    /**
    * Set the playback rate of the video. See https://developer.mozilla.org/en-US/docs/Web/API/HTMLMediaElement/playbackRate for more details
    * @method setPlaybackRate
    * @param {Number} r the playback rate
    */
    setPlaybackRate(r)
    {
        this._video.playbackRate = r;
    }

    /**
    * Get the current playback rate
    * @method getPlaybackRate
    * @return {Number} the playback rate
    */
    getPlaybackRate()
    {
        return this._video.playbackRate;
    }

    /**
    * Get the current video width
    * @method getWidth
    * @return {Number} the video width
    */
    getWidth()
    {
        return this._video.videoWidth;
    }

    /**
    * Get the current video height
    * @method getHeight
    * @return {Number} the video height
    */
    getHeight()
    {
        return this._video.videoHeight;
    }
}

// module exports
export { VideoTexture };
