import {Base} from '../Base';
import {getOrDefault} from '../helpers/Misc';
import {Loader} from '../helpers/Loader';
import {Font} from '../scene/Font';
import {Color} from '../core/Color';
import {EventEmitter} from '../helpers/EventEmitter';
import {StyledTextElement, TextLine} from '../text/TextUtils';

/**
* Triggered when the canvas has been redrawn, useful to synchronise Texture update and canvas refresh
* @event drawn
*/
const EVT_DRAWN = "drawn";

const TEXT_ALIGN_LEFT = "left";
const TEXT_ALIGN_RIGHT = "right";
const TEXT_ALIGN_CENTER = "center";

class RichText extends Base
{
    /**
    * RichText class uses standard HTML Text to format text drawn with a certain style in a Canvas. Supported HTML style tags are <b>, <strong>, <i>, <em>, <br>, <p>, <font> with attributes color and size. TODO : support of face attribute in <font>. Default font are predefined.
    * @class RichText
    * @extends Base
    * @constructor
    * @param {Object} params Parameters Object
    * @param {HTMLNode} params.htmlText the html text to draw in the canvas
    * @param {Number} [params.width=512] width of the inner canvas
    * @param {Number} [params.height=512] height of the inner canvas
    * @param {Number} [params.lineHeight=1] line height for the text
    * @param {Number} [params.margins=10] white space (margin) in pixel on all left, top, right and bottom of the canvas. Text will be drawn within these boundaries with automatic newline management.
    * @param {Number} [params.marginLeft=10] white space (margin) in pixel on left side.
    * @param {Number} [params.marginTop=10] white space (margin) in pixel on top side.
    * @param {Number} [params.marginRight=10] white space (margin) in pixel on right side.
    * @param {Number} [params.marginBottom=10] white space (margin) in pixel on bottom side.
    * @param {Number} [params.textSize=20] size, in pixels, of the text
    * @param {Color} [params.textColor=black] color the text default color
    * @param {Color} [params.textAlign="left"] text alignment in the canvas (margins included), can be one of "left", "center", "right".
    * @param {Color} [params.backgroundColor=white] color the background color
    * @param {Boolean} [params.drawBoundingBox=false] tell to draw the bounding boxes of every text blocs or not
    * @param {Number} [params.boundingBoxStrokeWidth=1] the lineWidth to use for bounding boxes drawing
    * @param {URL} params.fontURL url for the regular font file
    * @param {URL} params.fontItalicURL url for the italic font file
    * @param {URL} params.fontBoldURL url for the bold font file
    * @param {URL} params.fontBoldItalicURL url for the bold-italic font file
    *
    */
    constructor(params)
    {
        super(params);

        this._width = getOrDefault(params, "width", 512);
        this._height = getOrDefault(params, "height", 512);
        this._backgroundColor = getOrDefault(params, "backgroundColor", Color.white.clone());
        this._lineHeight = getOrDefault(params, "lineHeight", 1);

        this._margins = {left: 0, right:0, top:0, bottom:0};
        let margins = getOrDefault(params, "margins", 10);
        this._margins.left = getOrDefault(params, "marginLeft", margins);
        this._margins.right = getOrDefault(params, "marginRight", margins);
        this._margins.top = getOrDefault(params, "marginTop", margins);
        this._margins.bottom = getOrDefault(params, "marginBottom", margins);

        this._textSize = getOrDefault(params, "textSize", 20);
        this._textColor = getOrDefault(params, "textColor", Color.black.clone());
        this._textAlign = getOrDefault(params, "textAlign", TEXT_ALIGN_LEFT);
        this._boundingBoxStrokeWidth = getOrDefault(params, "boundingBoxStrokeWidth", 1);
        this._drawBoundingBox = getOrDefault(params, "drawBoundingBox", false);

        this._fontURL = getOrDefault(params, "fontURL", "fonts/Raleway-Regular.ttf");
        this._fontItalicURL = getOrDefault(params, "fontItalicURL", "fonts/Raleway-Regular-Italic.ttf");
        this._fontBoldURL = getOrDefault(params, "fontBoldURL", "fonts/Raleway-Bold.ttf");
        this._fontBoldItalicURL = getOrDefault(params, "fontBoldItalicURL", "fonts/Raleway-Bold-Italic.ttf");

        this._canvas = document.createElement("canvas");
        this._canvas.width = this._width;
        this._canvas.height = this._height;
        this._canvasContext = this._canvas.getContext("2d");

        this._canvas.events = new EventEmitter({scope:this._canvas});

        /*document.body.appendChild(this._canvas);
        this._canvas.style.position = "absolute";*/

        this._text = getOrDefault(params, "text", undefined);

        if(this._text)
        {
            this._DOMText = document.createElement("div")
            this._DOMText.innerHTML = this._text;
            this.styledText = this.parseDOMTree(this._DOMText);
        }
        else
        {
            console.error("no html text provided");
        }

        //for internal use
        this._lines = [];

        //loading font chain
        Loader.loadArrayBuffer(this._fontURL, (fotnData) => {
            this._defaultFontFile = fotnData;

            Loader.loadArrayBuffer(this._fontItalicURL, (fotnItalicData) => {
                this._defaultItalicFontFile = fotnItalicData;

                Loader.loadArrayBuffer(this._fontBoldURL, (fontBoldData) => {
                    this._defaultBoldFontFile = fontBoldData;

                    Loader.loadArrayBuffer(this._fontBoldItalicURL, (fontBoldItalicData) => {
                        this._defaultBoldItalicFontFile = fontBoldItalicData;

                        this.setup();
                    });
                });
            });
        });

    }

    /**
    * Setup bloc called after default font loading
    * @private
    * @method setup
    */
    setup()
    {
        this._defaultFont = new Font({fontFile: this._defaultFontFile});
        this._defaultItalicFont = new Font({fontFile: this._defaultItalicFontFile});
        this._defaultBoldFont = new Font({fontFile: this._defaultBoldFontFile});
        this._defaultBoldItalicFont = new Font({fontFile: this._defaultBoldItalicFontFile});

        this.render();
    }

    /**
    * Get canvas width
    * @method getWidth
    * @return {Number} canvas width
    */
    getWidth()
    {
        return this._width;
    }
    /**
    * Get canvas height
    * @method getHeight
    * @return {Number} canvas height
    */
    getHeight()
    {
        return this._height;
    }

    /**
    * Get the canvas
    * @method getCanvas
    * @return {Canvas} canvas
    */
    getCanvas()
    {
        return this._canvas;
    }

    /**
    * Stater method to process the html tree. Manage internally the array used afterward to contain all StyledTextElement.
    * @method parseDOMTree
    * @param {DOMNodeObject} baseNode
    * @return {Array} the resulting StyledTextElement array
    */
    parseDOMTree(baseNode)
    {
        var styledText = [];

        this.processDOMTree(baseNode, styledText);
        //console.log(styledText);
        return styledText;
    }

    /**
    * Recursive process of an html tree from it's main node. This method return a linear list (in the original text strings order) of StyledTextElement in order to cumulate all the tags a piece of string is attach to and the attributes of its immediate parent tag.
    * @method processDOMTree
    * @param {DOMNodeObject} baseNode
    * @param {Array} destArray the array of destination
    */
    processDOMTree(baseNode, destArray)
    {
        let i = 0;
        let node;

        //lazy <p> support : <p> has automatic margin, top and bottom, of 1 line-height, which is 2 <br> ;-)
        //TODO : support <p> top and bottom margin ?
        if(baseNode.tagName === "P")
        {
            if(baseNode.previousElementSibling.tagName === "P")
            {
                baseNode.innerHTML = baseNode.innerHTML + "<br>";
            }
            else
            {
                baseNode.innerHTML = "<br><br>" + baseNode.innerHTML + "<br>";
            }
        }

        while((node = baseNode.childNodes[i]))
        {

            let children = node.childNodes.length;

            if(children >= 1){
                this.processDOMTree(node, destArray);
            }
            else
            {
                //split the textContent
                let words = node.textContent.split(/\s/);

                //StyledTextElement array construction loop
                for(let w = 0; w < words.length; w++)
                {
                    let el = new StyledTextElement();
                    //surchage StyledTextElement
                    el.attributes = [];
                    el.tags = [];
                    el.setText(words[w]);

                    //reconstruct white space, lost after split, take first place, \n and "." is first as conditions
                    if( w !== 0)
                    {
                        el.text = " " + el.text;
                    }

                    //manage attributes at parent level as it's how it's done
                    if(node.parentNode.attributes)
                    {
                        for(let a=0; a<node.parentNode.attributes.length; a++)
                        {
                            el.attributes.push({name:node.parentNode.attributes[a].name,
                                                value:node.parentNode.attributes[a].value});
                        }
                    }

                    if(node.tagName){
                        el.tags.push(node.tagName);
                    }

                    let tempNode = node;
                    while(tempNode.parentNode)
                    {
                        if(tempNode.parentNode.tagName !== "DIV")//avoid to take the main enclosing span
                        {
                            el.tags.push(tempNode.parentNode.tagName);
                        }
                        tempNode = tempNode.parentNode;
                    }
                    destArray.push(el);
                }
            }
            i++;
        }

        return destArray;
    }


    /**
    * Render the input HTML text parsed in an array of StyledTextElement in the current canvas
    * @method render
    */
    render()
    {
        //background color
        if(this._backgroundColor !== Color.transparent.makeRGBAStringWithAlpha(0))
        {
            //console.log(this._backgroundColor, Color.transparent.makeRGBAStringWithAlpha(0));
            this._canvasContext.fillStyle = "#" + this._backgroundColor.getHexString();
            this._canvasContext.fillRect(0,0, this._width, this._height);
        }


        //x position of drawing (letter pos in x)
        var letterXOffset = this._margins.left;
        //y position of drawing (letter pos in y, or baseline)
        var lineYOffset = this._margins.top;
        //number of lines in the canvas
        var lineCount = 0;

        for(let i=0; i<this.styledText.length; i++)
        {
            let el = this.styledText[i];

            //select the font to use
            let currentFont = this._defaultFont;

            //define the font based on associated tags
            if(el.tags.indexOf("B") >= 0 || el.tags.indexOf("STRONG") >= 0)
            {
                currentFont = this._defaultBoldFont;
            }
            if (el.tags.indexOf("I") >= 0 || el.tags.indexOf("EM") >= 0)
            {
                currentFont = this._defaultItalicFont;
            }
            if (el.tags.indexOf("B") >= 0 && el.tags.indexOf("I") >= 0 ||
                el.tags.indexOf("STRONG") >= 0 && el.tags.indexOf("EM") >= 0)
            {
                currentFont = this._defaultBoldItalicFont;
            }

            let size = this._textSize;

            //test for new line <br> and add a new line to lineCount
            if(el.tags.indexOf("BR") >= 0)
            {
                lineYOffset += size * this._lineHeight;
                letterXOffset = this._margins.left;
                //increment the lineNb
                lineCount++;
                console.log("lineYOffset",lineYOffset);
            }

            let color = this._textColor;

            //is it the 1st run ?  place the baseline to the margin + font size
            if(lineYOffset === this._margins.top)
            {
                lineYOffset += size;
            }

            //test for new line from canvas width limit and reset offsets and add a new line to lineCount
            let tempWidth = letterXOffset + currentFont.getTextSize(el.text, size).width;

            if(tempWidth > this._width - this._margins.right)
            {
                lineYOffset += size * this._lineHeight;
                letterXOffset = this._margins.left;
                //to erase first space and avoid unused whitespace at line start
                el.text = el.text.replace(/^\s/, "");
                //increment the lineNb
                lineCount++;
            }

            //manage font tag attributes
            //TODO : manage font changes!!
            if(el.attributes)
            {
                for(let j=0; j<el.attributes.length; j++)
                {
                    if(el.attributes[j].name === "color")
                    {
                        color = new Color(el.attributes[j].value);
                    }
                    if(el.attributes[j].name === "size")
                    {
                        size = Number(el.attributes[j].value);
                    }
                }
            }
            //set the font to use
            el.setFont(currentFont);
            /*el.setPath(currentFont.getFont().getPath(el.text, letterXOffset, lineYOffset, size));*/
            let fontTextSize = currentFont.getTextSize(el.text, size);

            //store info in the object

            el.line = lineCount;
            el.setX(letterXOffset);

            el.setY(lineYOffset);

            el.setSize(size);
            //el.setColor("#" + color.getHexString());
            el.setColor(color);
            el.update();//refresh

            //populate the lines object for later align computation
            if(this._lines[lineCount])
            {
                let lineObj = this._lines[lineCount];
                lineObj.styledTextElements.push(el);
                lineObj.width += el.width;
            }
            else
            {
                let lineObj = new TextLine();
                lineObj.styledTextElements.push(el);
                lineObj.width += el.width;

                this._lines.push(lineObj);
            }

            //update the x offset for next el
            letterXOffset += fontTextSize.width;
        }

        //console.log(this._lines);

        //text-align loop, we adjust x positions of every text bloc
        for(let i=0; i<this._lines.length; i++)
        {
            let xDiff;
            let line = this._lines[i];

            //compute the x difference between current and desired align
            switch(this._textAlign)
            {
                case TEXT_ALIGN_CENTER :
                    xDiff = (this._width/2) - (this._margins.left + line.width/2);
                    break;

                case TEXT_ALIGN_RIGHT :
                    xDiff = (this._width-this._margins.left) - (this._margins.right + line.width);
                    break;
            }

            if(xDiff !== undefined)
            {
                for(let j=0; j<line.styledTextElements.length; j++)
                {
                    let el = line.styledTextElements[j];
                    el.setX(el.x + xDiff);
                    el.update();//refresh
                }
            }

        }

        //draw loop
        for(let i=0; i<this.styledText.length; i++)
        {
            let el = this.styledText[i];
            el.path.draw(this._canvasContext);

            if(this._drawBoundingBox)
            {
                let boundingBox = el.path.getBoundingBox();
                this._canvasContext.beginPath();
                this._canvasContext.moveTo(boundingBox.x1, boundingBox.y1);
                this._canvasContext.lineTo(boundingBox.x2, boundingBox.y1);
                this._canvasContext.lineTo(boundingBox.x2, boundingBox.y2);
                this._canvasContext.lineTo(boundingBox.x1, boundingBox.y2);
                this._canvasContext.lineTo(boundingBox.x1, boundingBox.y1 - this._boundingBoxStrokeWidth/2);
                this._canvasContext.lineWidth = this._boundingBoxStrokeWidth;
                this._canvasContext.stroke();
            }
        }

        //emit a custom event on the canvas to refresh texture when used
        this._canvas.events.trigger(EVT_DRAWN);
    }

}

// module exports
export { RichText };
