import {debug} from './Debug';
import {Component} from '../Component';

class Midi extends Component
{
    /**
     Gives access to the midi devices. This class requires the Web MIDI API to operate properly.

     @class Midi
     @constructor
     @param {Object} params the parameters object
     @param {String} params.in The Midi IN interface name we want to open.
     @param {String} params.out The Midi OUT interface name we want to open.
     @param {Function} params.onMidiIn The Midi IN callback we want to be called on a midi in event.

     @requires Web Midi API
     */
    constructor(params)
    {
        super(params);
        this.midioutname    = params.out;
        this.midiinname     = params.in;
        //callbacks
        this.onMidiIn       = params.onMidiIn;
        debug.log("params.onMidiIn="+params.onMidiIn);
        debug.log("this.onMidiIn="+this.onMidiIn);
        if (navigator.requestMIDIAccess)
        {
            navigator.requestMIDIAccess().then( this.onMIDIInit.bind(this), this.onMIDIReject.bind(this) );
        }
        else
        {
            console.warn("No MIDI support present in your browser. Try in another browser.");
        }
    }

    /**
     Web MIDI API success callback.
     @method onMIDIInit
     @private
     @param {Object} midi
     */
    onMIDIInit(midi)
    {
        this.midiAccess = midi;
        this.hookUpMIDI();
        this.midiAccess.onstatechange = this.hookUpMIDI;
    }

    /**
     Web MIDI API failure callback.
     @method onMIDIReject
     @private
     @param {Object} err
     */
    onMIDIReject(err)
    {
        alert(`
            The MIDI system failed to start.
            You're gonna have a bad time.
            Error: ${err.message}
        `);
    }

    /**
    * hookUpMIDI
    * @method hookUpMIDI
    * @private
    */
    hookUpMIDI()
    {
        if (this.midiinname !== undefined)
        {
            var inputs = this.midiAccess.inputs.values();
            debug.log("MIDI inputs on your system : ");
            for ( var input = inputs.next(); input && !input.done; input = inputs.next())
            {
                debug.log("input=", input);

                if (input.value.name === this.midiinname)
                {
                    debug.log("found midi interface IN : " + input.value.name);
                    input.value.onmidimessage = this.onMIDIMessage.bind(this);
                }
            }
        }
        if (this.midioutname !== undefined)
        {
            var outputs = this.midiAccess.outputs.values();
            debug.log("MIDI outputs on your system : ");
            for ( var output = outputs.next(); output && !output.done; output = outputs.next())
            {
                debug.log("output=", output);

                if (output.value.name === this.midioutname)
                {
                    debug.log("found midi interface OUT : " + output.value.name);
                    this.midioutput = output.value;
                }
            }
        }
    }

    /**
    * onMIDIMessage
    * @method onMIDIMessage
    * @param {Object} event MIDI event
    * @private
    */
    onMIDIMessage(event)
    {
        debug.log("this.onMidiIn=" + this.onMidiIn);
        if (this.onMidiIn !== undefined)
        {
            //channel message data1 data2
            let message = event.data[0] & 0xf0;
            let channel = event.data[0] - message;
            let data1 = event.data[1];
            let data2 = event.data[2];
            this.onMidiIn(channel, message, data1, data2);
        }
    }

    /**
     Sends a Control Change MIDI message.
     @method ControlChange
     @param {Number} channel MIDI channel [0-15]
     @param {Number} cc ControlChange number [0-127]
     @param {Number} val ControlChange value [0-127]
     @example
         midi.ControlChange(2,11,50);
     */
    ControlChange(channel, cc, val)
    {
        if (this.midioutput === undefined) {
            return;
        }

        var ccMessage;
        var m = 0xB0+channel;
        ccMessage = [m, cc, val];
        this.midioutput.send(ccMessage);
    }

    /**
     Sends a NoteOn MIDI message.
     @method NoteOn
     @param {Number} channel MIDI channel [0-15]
     @param {Number} note Midi Note number [0-127]
     @param {Number} vel note MIDI velocity [0-127]
     @example
         midi.NoteOn(0,60,127);
     */
    NoteOn(channel, note, vel)
    {
        if (this.midioutput === undefined) {
            return;
        }

        var ccMessage;
        var m = 0x90+channel;
        ccMessage = [m, note, vel];
        this.midioutput.send(ccMessage);
    }

    /**
     Sends a NoteOff MIDI message.
     @method NoteOff
     @param {Number} channel MIDI channel [0-15]
     @param {Number} note Midi Note number [0-127]
     @param {Number} vel note MIDI velocity [0-127]
     @example
         midi.NoteOff(0,60,0);
     */
    NoteOff(channel, note, vel)
    {
        if (this.midioutput === undefined) {
            return;
        }

        var ccMessage;
        var m = 0x80+channel;
        ccMessage = [m, note, vel];
        this.midioutput.send(ccMessage);
    }

}
/**
 * Note On
 *
 * @property NoteOn
 * @type Number
 * @static
 * @final
 */
Midi.NoteOn = 144;
/**
 * Note Off
 *
 * @property NoteOff
 * @type Number
 * @static
 * @final
 */
Midi.NoteOff = 128;
/**
 * After touch
 *
 * @property Aftertouch
 * @type Number
 * @static
 * @final
 */
Midi.Aftertouch = 160;
/**
 * Control change
 *
 * @property ControlChange
 * @type Number
 * @static
 * @final
 */
Midi.ControlChange = 176;
/**
 * Program change
 *
 * @property ProgramChange
 * @type Number
 * @static
 * @final
 */
Midi.ProgramChange = 192;


// module exports
export { Midi };
