import {Vector3} from '../core/Vector3';

/*
 * MeshFX provides a set of static methods to work on 3D models verticies and to apply various effects on it.
 *
 * @class MeshFX
 */
class MeshFX
{

    /*
    * Noise the vertices of the Mesh
    * @static
    * @method Noise
    * @param {Mesh} mesh
    * @param {Vector3} amount
    */
    static Noise(mesh, amount)
    {
        var vertices = mesh.getVertices();
        for (var i =0;i<vertices.length;++i)
        {
            var noise = new Vector3(Math.random()-0.5, Math.random()-0.5, Math.random()-0.5);
            vertices[i] = new Vector3(vertices[i].x+noise.x*amount, vertices[i].y+noise.y*amount, vertices[i].z+noise.z*amount);

        }
        mesh.setVertices(vertices);
    }


    static Nothing(mesh)
    {
        var vertices = mesh.getVertices();
        for (var i =0;i<vertices.length;++i)
        {
            vertices[i] = new Vector3(vertices[i].x, vertices[i].y, vertices[i].z);
        }
        mesh.setVertices(vertices);
    }

    /*
    * Offsets the vertices of the Mesh
    * @static
    * @method Offset
    * @param {Mesh} mesh
    * @param {Vector3} offset
    */
    static Offset(mesh, offset)
    {
        var vertices = mesh.getVertices();
        for (var i =0;i<vertices.length;++i)
        {
            vertices[i] = new Vector3(vertices[i].x+offset.x, vertices[i].y+offset.y, vertices[i].z+offset.z);

        }
        mesh.setVertices(vertices);
    }

    /*
    * Scales the vertices of the Mesh
    * @static
    * @method Scale
    * @param {Mesh} mesh
    * @param {Vector3} scale
    */
    static Scale(mesh, scale)
    {
        var vertices = mesh.getVertices();
        for (var i =0;i<vertices.length;++i)
        {
            vertices[i] = new Vector3(vertices[i].x*scale.x, vertices[i].y*scale.y, vertices[i].z*scale.z);

        }
        mesh.setVertices(vertices);
    }

}

// module exports
export { MeshFX };
