import * as THREE from 'three';

import {getOrDefault} from '../helpers/Misc';
import {Color} from '../core/Color';
import {Transform} from './Transform';
import {Vector3} from "../core/Vector3";

class Light
{
    /**
     * Light will enable object to be seen in space. Camera and Light are requiered for a basic scene.
     *
     * @class Light
     * @constructor
     * @param {Object} params parameters object
     * @param {String} [params.type:"point"] one of "point", "directional", "ambient", "spot"
     * @param {Number} [params.intensity=1]
     * @param {Number} [params.distance=1000] distance is taken in account only for point lights
     * @param {Color} [params.color=Color.white]
     */
    constructor(params)
    {
        this.type = getOrDefault(params, "type", "point");

        switch (this.type)
        {
            case "point":
                this.light = new THREE.PointLight( 0xffffff );
                this.light.distance = 1000;
                break;

            case "directional":
                this.light = new THREE.DirectionalLight( 0xffffff );
                this.light.position.set(1, 1, 1).normalize();
                break;

            case "ambient":
                this.light = new THREE.AmbientLight( 0x404040 ); // soft white light
                this.light.position.set(1, 1, 1).normalize();
                break;

            case "spot":
                this.light = new THREE.SpotLight( 0xffffff );
                //this.light.position.set(1, 1, 1).normalize();
                this.target = new THREE.Object3D();
                this.light.target = this.target;
                break;

            default:
                this.light = new THREE.PointLight( 0xffffff );
                this.type = "point";
        }


        this.light.intensity = getOrDefault(params, "intensity", 1);
        this.light.distance = getOrDefault(params, "distance", 5000);
        this.light.color = getOrDefault(params, "color", Color.white);

        this.transform = new Transform(this/*.light*/);
    }

    /**
    * Sets the light's intensity
    * @method setIntensity
    * @param {Object} val
    */
    setIntensity(val)
    {
        this.light.intensity =  val;
    }

    /**
    * Gets the light's intensity
    * @method getIntensity
    * @param {Object} val
    */
    getIntensity()
    {
        return this.light.intensity;
    }

    /**
    * Sets the light's distance factor
    * @method setDistance
    * @param {Number} val
    */
    setDistance(val)
    {
        if(this.type === "point" || this.type === "spot" ){
            this.light.distance =  val;
        }
    }

    /**
    * Gets the light's distance factor
    * @method getDistance
    * @return {Number} the light distance factor
    */
    getDistance()
    {
        return this.light.distance;
    }
    /**
     * setColor
     * @method setColor
     * @param {Color} color
     */
    setColor(color){
        this.light.color = color;
    }
    /**
     * getColor
     * @method getColor
     * @return {Color} color
     */
    getColor(){
        return this.light.color;//TODO : return a Mobilizing object, not a Three one
    }
    /**
     * Maximum extent of the spotlight, in radians, from its direction. Should be no more than Math.PI/2. The default is Math.PI/3.
     * @method setAngle
     * @param {Number} angle the spot angle
     */
    setAngle(val)
    {
        if(this.type === "spot")
        {
            this.light.angle = val;
        }
    }
    /**
     * getAngle
     * @method getAngle
     * @return {Number} the angle
     */
    getAngle()
    {
        return this.light.angle;
    }

    /**
    * Position in space to where the spot light points
    * @method setTargetPosition
    * @param {Object} x
    * @param {Object} y
    * @param {Object} z
    */
    setTargetPosition(arg1, arg2, arg3)
    {
        if(this.type === "spot")
        {
            if(arg1 instanceof Vector3)
            {
                this.target.position.set(arg1.x, arg1.y, arg1.z);
                this.target.updateMatrixWorld();
            }
            else
            {
                this.target.position.set(arg1, arg2, arg3);
                this.target.updateMatrixWorld();
            }
        }
    }
    /**
    * Smoothness of the spot impact shape (makes the light circle blurry)
    * @method setPenumbra
    * @param {Object} val between 0 and 1
    */
    setPenumbra(val)
    {
        this.light.penumbra = val;
    }

    /**
     * setCastShadow
     * @method setCastShadow
     * @param {Boolean} enabled
     */
    setCastShadow(enabled)
    {
        this.light.castShadow = enabled;
    }

    /**
     * set the Shadow map Size
     * @method setShadowSize
     * @param {Number} width
     * @param {Number} height
     */
    setShadowSize(width,height)
    {
        this.light.shadow.mapSize.width = width; //shadow map size
        this.light.shadow.mapSize.height = height; //shadow map size
    }

    /**
     * setShadowDistance
     * @method setShadowDistance
     * @param {Number} near min shadow distance
     * @param {Number} far max shadow distance
     */
    setShadowDistance(near,far)
    {
        this.light.shadow.camera.near = near; //min shadow distance
        this.light.shadow.camera.far = far; //max shadow distance
    }
}

// module exports
export { Light };
