import * as THREE from 'three';

import {debug} from '../helpers/Debug';

class Engine3D
{
    /**
     * Engine3D class contains the technical elements that bind Mobilizing.js API to a 3D rendering library. Currently, it's Three.js that is used to acheive 3D rendering. In the future, we can imagine to use an other library or a home made Engine.
     *
     * @class Engine3D
     * @constructor
     * @param {Canvas} canvas the canvas to use, if any, otherwise the Engine produce one.
     * @param {Context} context the Mobilizing.js context to use, if any.
     */
    constructor(params)
    {

        //we try to use the canvas
        //try
        if (params.canvas)
        {
            const canvas = params.canvas;

            debug.log("try to attach the Canvas...",canvas);
            //Mobilizing.renderer = new THREE.WebGLRenderer({canvas: document.getElementById('canvas')}); //FIXME : Ejecta
            //this.renderer = new THREE.WebGLRenderer({canvas: document.getElementById(canvas)});
            this.renderer = new THREE.WebGLRenderer({canvas: canvas, /*antialias: true, preserveDrawingBuffer: true*/});
            this.engine = "webgl";

            if(this.renderer.devicePixelRatio === undefined)
            {
                this.renderer.devicePixelRatio = window.devicePixelRatio;
            }
            this.renderer.setPixelRatio( window.devicePixelRatio ? window.devicePixelRatio : 1 ); //force retina
            this.renderer.setSize( canvas.width/window.devicePixelRatio, canvas.height/window.devicePixelRatio);

            canvas.style.width = canvas.width/window.devicePixelRatio +"px";
            canvas.style.height = canvas.height/window.devicePixelRatio +"px";
            this.canvas = canvas;
            this.canvas.retinaResolutionEnabled = true;

            this.windowWidth = this.canvas.clientWidth;
            this.windowHeight = this.canvas.clientHeight;
            debug.log("existing CANVAS",this.renderer);
        }
        else
        {
            debug.log("retrieve Canvas failed, create a new canvas");

            //this.renderer = new THREE.WebGLRenderer({antialias: true, preserveDrawingBuffer: true, alpha:true});
            this.renderer = new THREE.WebGLRenderer();

            this.engine = "webgl";
            this.renderer.setPixelRatio( window.devicePixelRatio ? window.devicePixelRatio : 1 ); //force retina

            if(this.renderer.devicePixelRatio === undefined)
            {
                this.renderer.devicePixelRatio = window.devicePixelRatio;
            }
            this.renderer.setSize( window.innerWidth, window.innerHeight);

            document.body.appendChild( this.renderer.domElement );
            this.canvas = this.renderer.domElement;
            this.canvas.retinaResolutionEnabled = true;

            this.windowWidth = window.innerWidth;
            this.windowHeight = window.innerHeight;
        }

        debug.log("engine = " + this.engine);
        this.renderer.setClearColor(0x000000, 1.0);
        this.renderer.autoClear = true; //false

        debug.log("window pixel ratio = " + window.devicePixelRatio);

        /*
        this.renderer.setPixelRatio( window.devicePixelRatio ); //force retina
        this.renderer.setSize( window.innerWidth, window.innerHeight);*/
    }

    /**
    * Returns the current Engine rendering canvas
    * @method getCanvas
    * @return {Canvas} the canvas being used.
    */
    getCanvas()
    {
        return this.canvas;
    }

}

// module exports
export { Engine3D };
