import {getOrDefault, noop} from '../helpers/Misc';
import {Mesh} from '../shape/Mesh';
import {Color} from '../core/Color';
import {Vector2} from '../core/Vector2';
import {Component} from '../Component';
import {Clickable} from './Clickable';
import {_Math} from '../helpers/Math';
import {Texture} from "../texture/Texture";

const BT_STATE_PRESSED = "pressed";

const BT_STATE_RELEASED = "released";

class Button extends Component
{
    /**
     * A Button is a special kind of 3D object that is clickable and that you can use to build Graphical User Interfaces (GUI).
     *
     * @class Button
     * @constructor
     * @param {Object} params parameters object.
     * @param {Camera} params.camera the camera used for picking.
     * @param {String} params.text text to render (can be empty).
     * @param {Number} params.textSize the text size
     * @param {Object} params.font font to use.
     * @param {Number} params.width width in pixels.
     * @param {Number} params.height height in pixels.
     * @param {Number} params.cutOff the size of the cutOff
     * @param {Color} params.strokeColor
     * @param {Color} params.fillColor
     * @param {Color} params.pressFillColor
     * @param {Color} params.hoverFillColor
     * @param {Function} [params.onPress]
     * @param {Function} [params.onRelease]
     * @param {Function} [params.onEnter]
     * @param {Function} [params.onLeave]
     * @param {URL} [params.fontURL] url for the regular font file
     * @param {URL} [params.fontItalicURL] url for the italic font file
     * @param {URL} [params.fontBoldURL] url for the bold font file
     * @param {URL} [params.fontBoldItalicURL] url for the bold-italic font file
     * @example
     *     //TODO
     */
    constructor(params)
    {
        super(params);

        this.camera = params.camera;// requiered
        this.pointer = getOrDefault(params, "pointer", undefined);
        this.mesh = getOrDefault(params, "mesh", undefined);
        this.width = getOrDefault(params, "width", 3);
        this.height = getOrDefault(params, "height", 1);
        this.radius = getOrDefault(params, "radius", undefined);
        this.sideCount = getOrDefault(params, "sideCount", 6);

        this.cutOff = getOrDefault(params, "cutOff", this.height / 3);
        this.strokeWidth = getOrDefault(params, "strokeWidth", 0.1);

        this.strokeColor = getOrDefault(params, "strokeColor", Color.mobilizing.clone());
        this.fillColor = getOrDefault(params, "fillColor", Color.white.clone());
        this.pressFillColor = getOrDefault(params, "pressFillColor", Color.mobilizing.clone());
        this.hoverFillColor = getOrDefault(params, "hoverFillColor", Color.mobilizingAlternate.clone());

        this.onPress = getOrDefault(params, "onPress", noop);
        this.onRelease = getOrDefault(params, "onRelease", noop);
        this.onEnter = getOrDefault(params, "onEnter", noop);
        this.onLeave = getOrDefault(params, "onLeave", noop);

        this._fontURL = getOrDefault(params, "fontURL", "fonts/Raleway-Regular.ttf");
        this._fontItalicURL = getOrDefault(params, "fontItalicURL", "fonts/Raleway-Regular-Italic.ttf");
        this._fontBoldURL = getOrDefault(params, "fontBoldURL", "fonts/Raleway-Bold.ttf");
        this._fontBoldItalicURL = getOrDefault(params, "fontBoldItalicURL", "fonts/Raleway-Bold-Italic.ttf");

        this.text = getOrDefault(params, "text", undefined);
        this.textSize = getOrDefault(params, "textSize", 40);
        //main node
        this.root = new Mesh({primitive: "node"});

        this.transform = this.root.transform;

        //to get the state
        this.state = BT_STATE_RELEASED;

        //create the texture from text
        if(this.text)
        {
            //FIXME -> quand on a un radius pour le poly faire une texture carrÃ©
            let canvasWidth;
            if(this.width < 100)
            {
                canvasWidth = 512;
            }
            else
            {
                canvasWidth = this.width;
            }

            let canvasHeight;
            if(this.height < 100)
            {
                canvasHeight = canvasWidth / (this.width / this.height);
            }
            else
            {
                canvasHeight = this.height;
            }

            if(this.radius)
            {
                let twoSquareThree = 2 / Math.sqrt(3);
                canvasHeight = canvasWidth * twoSquareThree;
            }

            //label normal state
            let richText = new Mobilizing.RichText({width: canvasWidth,
                                                    height: canvasHeight,
                                                    text: this.text,
                                                    marginTop:canvasHeight/2 - this.textSize/2,
                                                    backgroundColor: Color.transparent.makeRGBAStringWithAlpha(0),
                                                    textColor: Color.mobilizing.clone(),
                                                    textAlign: "center",
                                                    textSize: this.textSize,
                                                    fontURL: this._fontURL,
                                                    fontItalicURL: this._fontItalicURL,
                                                    fontBoldURL: this._fontBoldURL,
                                                    fontBoldItalicURL: this._fontBoldItalicURL});

            //label pressed state
            let pressRichText = new Mobilizing.RichText({width: canvasWidth,
                                                         height: canvasHeight,
                                                         text: this.text,
                                                         marginTop:canvasHeight/2 - this.textSize/2,
                                                         backgroundColor: Color.transparent.makeRGBAStringWithAlpha(0),
                                                         textColor: Color.white.clone(),
                                                         textAlign: "center",
                                                         textSize: this.textSize,
                                                         fontURL: this._fontURL,
                                                         fontItalicURL: this._fontItalicURL,
                                                         fontBoldURL: this._fontBoldURL,
                                                         fontBoldItalicURL: this._fontBoldItalicURL});

            this.textTexture = new Texture({canvas: richText.getCanvas()});
            this.pressTextTexture = new Texture({canvas: pressRichText.getCanvas()});
            /*document.body.appendChild(richText.getCanvas());
            richText.getCanvas().style.position = "absolute";
            richText.getCanvas().style.top = "0px";
            richText.getCanvas().style.left = "0px";*/
        }
        //construct the default mesh & stroke
        if(!this.mesh)
        {
            this.generateDefaultMesh();
        }

        this.root.transform.addChild(this.mesh.transform);
    }

    setup()
    {
        super.setup();
        let context = this.getContext();

        this.clickable = new Clickable({camera: this.camera,
                                        target: this.mesh,
                                        pointer: this.pointer,
                                        onPress: () =>
                                        {
                                            if(this._active)
                                            {
                                                this.mesh.material.setColor(this.pressFillColor);
                                                if(this.pressTextTexture)
                                                {
                                                    this.texturedMesh.material.setTexture(this.pressTextTexture);
                                                }
                                                this.onPress();
                                                this.state = BT_STATE_PRESSED;
                                            }
                                        },

                                        onRelease: () =>
                                        {
                                            if(this._active)
                                            {
                                                this.mesh.material.setColor(this.hoverFillColor);
                                                if(this.textTexture)
                                                {
                                                    this.texturedMesh.material.setTexture(this.textTexture);
                                                }
                                                this.onRelease();
                                                this.state = BT_STATE_RELEASED;
                                            }
                                        },

                                        onEnter: () =>
                                        {
                                            if(this._active)
                                            {
                                                this.mesh.material.setColor(this.hoverFillColor);
                                                this.onEnter();
                                            }
                                        },

                                        onLeave: () =>
                                        {
                                            if(this._active)
                                            {
                                                this.mesh.material.setColor(this.fillColor);
                                                if(this.textTexture)
                                                {
                                                    this.texturedMesh.material.setTexture(this.textTexture);
                                                }
                                                this.onLeave();
                                            }
                                        }
                                       });
        context.addComponent(this.clickable);
        this.clickable.setup();
        this.on();
    }

    /**
    * Activate the button
    * @method on
    */
    on()
    {
        super.on();
        this.mesh.material.setOpacity(1);
        this.strokeMesh.material.setOpacity(1);
        if(this.text)
        {
            this.texturedMesh.material.setOpacity(1);
        }
    }
    /**
    * deactivate the button, set its opacity to 30 %
    * @method off
    */
    off()
    {
        super.off();
        this.mesh.material.setOpacity(0.3);
        this.strokeMesh.material.setOpacity(0.3);
        if(this.text)
        {
            this.texturedMesh.material.setOpacity(0.3);
        }
    }

    update()
    {

    }

    /**
    * Generate the vertices and meshes for the default button. Called internally only
    * @private
    * @method generateDefaultMesh
    */
    generateDefaultMesh()
    {
        //vertices
        let w = this.width/2;
        let h = this.height/2;
        //this.cutOff = h/2;
        //let cutOffXOffset = 0;

        this.vertex = [];

        //manage special case of squared size : will be an hexagon!
        if(this.radius)
        {

            let parts = this.sideCount;
            let radius = this.radius;

            for(let i=0; i<parts; i++){
                //arc(a, b, c, d, start, stop, mode)
                this.vertex.push(
                    new Vector2( Math.cos(_Math.degToRad(360/parts/2) + (Math.PI*2/parts) * i) * radius,
                                Math.sin(_Math.degToRad(360/parts/2) + (Math.PI*2/parts) * i) * radius)
                )
            }
        }
        else
        {
            this.vertex.push(new Vector2(-w + this.cutOff, h));
            this.vertex.push(new Vector2( w - this.cutOff, h));
            this.vertex.push(new Vector2( w, h - this.cutOff));
            this.vertex.push(new Vector2( w, -h + this.cutOff));
            this.vertex.push(new Vector2( w - this.cutOff, -h));
            this.vertex.push(new Vector2(-w + this.cutOff, -h));
            this.vertex.push(new Vector2(-w, -h + this.cutOff));
            this.vertex.push(new Vector2(-w, h - this.cutOff));
        }

        //console.log(this.vertex);

        this.topLeftIndex = 0;
        this.topRightIndex = 1;
        this.bottomRightIndex = 4;
        this.bottomLeftIndex = 5;

        this.mesh = new Mesh({primitive: "custom"});
        this.mesh.generateFillMesh(this.vertex);
        this.mesh.material.setTransparent(true);

        if(this.text)
        {
            this.texturedMesh = new Mesh({primitive: "custom"});
            this.texturedMesh.generateFillMesh(this.vertex);
            this.texturedMesh.material.setTransparent(true);
            this.texturedMesh.material.setTexture(this.textTexture);
            this.root.transform.addChild(this.texturedMesh.transform);
        }

        this.strokeMesh = Mesh.generateStrokeMesh(this.mesh, this.strokeWidth);
        this.strokeMesh.material.setColor(this.strokeColor);
        this.strokeMesh.material.setTransparent(true);

        this.root.transform.addChild(this.strokeMesh.transform);
    }

    /**
    * Adapt a corner of the shape, for grouping buttons together
    * @method adaptCorner
    * @param {String} mode cutOff, straight
    * @param {String} corner topLeft, topRight, bottomRight,bottomLeft
    */
    adaptCorner(mode, corner)
    {
        if(this.width !== this.height)
        {
            let vertex = null;

            switch(corner)
            {
                case "topLeft" :
                    vertex = this.mesh._geometry.vertices[this.topLeftIndex];

                    if(mode === "straight")
                    {
                        vertex.x = -this.width/2;
                    }
                    else if(mode === "cutOff")
                    {
                        vertex.x = -this.width/2 + this.cutOff;
                    }
                    this.mesh.updateMesh();
                    this.regenerateStrokeGeometry(this.mesh);

                    break;

                case "topRight" :
                    vertex = this.mesh._geometry.vertices[this.topRightIndex];

                    if(mode === "straight")
                    {
                        vertex.x = this.width/2;
                    }
                    else if(mode === "cutOff")
                    {
                        vertex.x = this.width/2 - this.cutOff;
                    }
                    this.mesh.updateMesh();
                    this.regenerateStrokeGeometry(this.mesh);

                    break;

                case "bottomRight" :
                    vertex = this.mesh._geometry.vertices[this.bottomRightIndex];

                    if(mode === "straight")
                    {
                        vertex.x = this.width/2;
                    }
                    else if(mode === "cutOff")
                    {
                        vertex.x = this.width/2 - this.cutOff;
                    }
                    this.mesh.updateMesh();
                    this.regenerateStrokeGeometry(this.mesh);

                    break;

                case "bottomLeft" :
                    vertex = this.mesh._geometry.vertices[this.bottomLeftIndex];

                    if(mode === "straight")
                    {
                        vertex.x = -this.width/2;
                    }
                    else if(mode === "cutOff")
                    {
                        vertex.x = -this.width/2 + this.cutOff;
                    }
                    this.mesh.updateMesh();
                    this.regenerateStrokeGeometry(this.mesh);

                    break;
            }
            this.mesh.generateFlatUVs();
        }
    }

    /**
    * regenerate the geometry of the mesh for further update
    * @method regenerateStrokeGeometry
    * @private
    * @param {Mesh} the mesh to regenerate the stroke for
    */
    regenerateStrokeGeometry(mesh)
    {
        this.strokeMesh.updateStroke(mesh, this.strokeWidth);
    }

    /**
    * Can be used to simulate a pressed event when necessary (i.e. when a keyboard event should modify the button state).
    *
    * @method fakePress
    */
    fakePress()
    {
        this.fakePressed = true;
    }

    /*update()
    {

    }*/

}

// module exports
export { Button };
