import {Component} from '../Component';

/*
 * Profiler class should not be used by users
 *
 * @class Profiler
 * @private
 */

class Profiler extends Component
{

    /**
     * Profiler is a Profiler component.
     *
     *
     * @example
     *     //to do
     *
     * @class Profiler
     * @constructor
     * @extends Component
     * @param {Object} params Config parameters
    */
    constructor(params){
        super(params);
        this.timers          = [];
        this.current_frame   = 0;
        this.last_time       = 0;
        this.initialize();
    }

    initialize()
    {
        this.last_time = this.getTime();
    }


    update()
    {
        var t = this.getTime();
        var dt = t - this.last_time;
        this.last_time = t;
        var frame = this.timers[this.current_frame];
        if (!frame)
        {
            frame = {};
            this.timers[this.current_frame] = frame;
        }
        this.timers[this.current_frame].dt = dt;
        this.current_frame++;

        this.logTimers();
    }


    startTimer()
    {
        //var t = Profiler.getTime();
        //Profiler.timers[Profiler.current_frame].
    }


    getTime()
    {
        return Date.now();
    }


    logTimers()
    {
        console.log("Profiler timers : " + this.timers.map((timer) => timer.dt).join(' '));
    }
}


// module exports
export { Profiler };
