/**
 * Math class gives easy access to some basics calculations that are frequently used in interactive computer graphics
 *
 * @class Math
 * @example
 *     Mobilizing.Math.map(10, 0,20, -10,10);
 */

class _Math
{

    /**
     * Generates a random number between 2 float values
     * @method randomFromTo
     * @static
     * @param {float} from
     * @param {float} to
     */
    static randomFromTo(from, to) {
        return Math.random() * (to - from) + from;
    }

    /**
     * Maps a number between 2 sets of float values
     * @method map
     * @static
     * @param {float} value the value to map
     * @param {float} low1 the 1st origin value
     * @param {float} high1 the 2nd destination value
     * @param {float} low2 the 1st destination value
     * @param {float} high2
     */
    static map(value, low1, high1, low2, high2) {
        return low2 + (high2 - low2) * (value - low1) / (high1 - low1);
    }

    /**
     * Converts degrees to radians
     * @static
     * @method degToRad
     * @param {float} val
     */
    static degToRad(val){
        var degreeToRadiansFactor = Math.PI / 180;
        return val * degreeToRadiansFactor;
    }

    /**
     * Converts radians to degrees
     * @static
     * @method radToDeg
     * @param {float} val
     */
    static radToDeg(val){
        var radianToDegreesFactor = 180 / Math.PI ;
        return val * radianToDegreesFactor;
    }

    /**
     * Calculates the distance between two 2D points
     * @static
     * @method dist
     * @param {float} x1
     * @param {float} y1
     * @param {float} x2
     * @param {float} y2
     */
    static dist(x1,y1, x2,y2){
        return Math.sqrt( ((x2-x1)*(x2-x1)) + ((y2-y1)*(y2-y1)) );
    }

    /**
     * Lerps a value to the destination value with the given amount
     * @static
     * @method lerp
     * @param {float} value
     * @param {float} dest
     * @param {float} amount
     */
    static lerp(value,dest,amount){
        return (value + (dest-value)*amount);
    }

    /**
     * Clamp a value between a minimum and a maximum
     * @static
     * @method clamp
     * @param {Number} value
     * @param {Number} min
     * @param {Number} max
     */
    static clamp(value, min, max){
        return (Math.min(max, Math.max(min, value)));
    }

    /**
     * Finds if a point is inside an array of vertices
     * @static
     * @method pointIsInside
     * @param {Number} x
     * @param {Number} y
     * @param {Vector2 Array} points vertices {x,y}
     */
    static pointIsInside(x,y, points)
    {
        let i, j = points.length - 1;
        let oddNodes = false;

        for ( i=0; i < points.length; i++ )
        {
            if (points[i].y < y && points[j].y >= y ||
                points[j].y < y && points[i].y >= y)
            {
                if (points[i].x + (y - points[i].y) / (points[j].y - points[i].y) * (points[j].x - points[i].x) < x)
                {
                    oddNodes = !oddNodes;
                }
            }
            j=i;
        }

        return oddNodes;
    }

    static intersectionPoint(line1, line2)
    {
        let a1 = line1[1].x - line1[0].x;
        let b1 = line2[0].x - line2[1].x;
        let c1 = line2[0].x - line1[0].x;

        let a2 = line1[1].y - line1[0].y;
        let b2 = line2[0].y - line2[1].y;
        let c2 = line2[0].y - line1[0].y;


        let t;
        //treat special case : points are on the same lines!
        if(b1*c2 === 0 && b2*c1 === 0 && a2*b1  === 0 && a1*b2  === 0)
        {
            t = 0;
        }
        else
        {
            t = (b1*c2 - b2*c1) / (a2*b1 - a1*b2);
        }


        return {
            x: line1[0].x + t * (line1[1].x - line1[0].x),
            y: line1[0].y + t * (line1[1].y - line1[0].y)
        };
    }

}

// module exports
export { _Math };
