import {getOrDefault} from '../helpers/Misc';
import {Color} from '../core/Color';

/**
 * StyledLetter is a class that helps to represents a letter with its style for later drawing.
 * @class StyledLetter
 * @constructor
 * @param {Object} params Parameters Object
 * @param {String} params.letter the letter's string (character)
 * @param {Number} params.size the size of the font
 * @param {Font} params.font a Mobilizing font reference
 * @param {Color} params.color a Mobilizing Color
 */
class StyledLetter
{
    constructor(params)
    {
        this.letter = getOrDefault(params, "letter", "");
        this.size = getOrDefault(params, "size", 40);
        this.font = getOrDefault(params, "font", undefined);//mobilizing font
        // this.path; //openType Path
        // this.boundingBox; //derived from path

        this.color = getOrDefault(params, "color", Color.black.clone());

        this.x = 0;
        this.y = 0;
        this.width = 0;
        this.height = 0;

        if(this.font)
        {
            this.update();
        }
    }

    /**
    * Updates properties according to the new settings
    * @method update
    */
    update()
    {
        let size = this.font.getTextSize(this.letter, this.size);
        this.width = size.width;
        this.height = size.height;

        this.path = this.font.getFont().getPath(this.letter, this.x, this.y, this.size);
        this.path.fill = "#" + this.color.getHexString(); //this.color;
        this.boundingBox = this.path.getBoundingBox();
    }
    /**
    * Defines the letter's string
    * @method setLetter
    * @param {String} letter
    */
    setLetter(val)
    {
        this.letter = val;
    }
    /**
    * Defines the letter's x coord
    * @method setX
    * @param {Number} x
    */
    setX(val)
    {
        this.x = val;
    }
    /**
    * Defines the letter's y coord
    * @method setY
    * @param {Number} y
    */
    setY(val)
    {
        this.y = val;
    }
    /**
    * Defines the letter's font size
    * @method setSize
    * @param {Number} size
    */
    setSize(val)
    {
        this.size = val;
    }
    /**
    * Defines the letter's color
    * @method setColor
    * @param {Color} color
    */
    setColor(color)
    {
        this.color = color;
    }
    /**
    * Defines the letter's font
    * @method setFont
    * @param {Font} font
    */
    setFont(font)
    {
        this.font = font;
    }
}

/**
 * StyledTextElement is a simple class to organise text elements extacted from HTML text. It contains an array of StyledLetter.
 * @class StyledTextElement
 * @constructor
 * @param {Object} params Parameters Object
 * @param {String} params.text the text string (character)
 * @param {Number} params.size the size of the font
 * @param {Font} params.font a Mobilizing font reference
 * @param {Color} params.color a Mobilizing Color
 */
class StyledTextElement
{
    constructor(params)
    {
        this.text = getOrDefault(params, "text", "");
        this.font = getOrDefault(params, "font", undefined);
        this.size = getOrDefault(params, "size", 40);
        this.color = getOrDefault(params, "color", Color.black.clone());

        // this.path;
        // this.boundingBox;

        this.line = 0;

        this.x = 0;
        this.y = 0;
        this.width = 0;
        this.height = 0;

        this.styledLetters = [];

        if(this.font)
        {
            this.update();
        }
    }
    /**
    * Updates properties according to the new settings (including styledLetters array properties)
    * @method update
    */
    update()
    {
        let size = this.font.getTextSize(this.text, this.size);
        this.width = size.width;
        this.height = size.height;

        this.path = this.font.getFont().getPath(this.text, this.x, this.y, this.size);
        this.path.fill = "#" + this.color.getHexString(); //this.color;
        this.boundingBox = this.path.getBoundingBox();

        let baseWidth = 0;

        for(let i=0; i<this.styledLetters.length; i++)
        {
            let styledLetter = this.styledLetters[i];
            styledLetter.font = this.font;
            if(i === 0)
            {
                styledLetter.setX(this.x);
                baseWidth = styledLetter.width;
            }
            else
            {
                styledLetter.setX(this.x + baseWidth);
                baseWidth += styledLetter.width;
            }
            styledLetter.setY(this.y);
            styledLetter.setSize(this.size);
            styledLetter.setColor(this.color);
            styledLetter.update();
        }
    }
    /**
    * Defines the letter's font
    * @method setFont
    * @param {Font} font
    */
    setFont(font)
    {
        this.font = font;
    }
    /**
    * Defines the letter's color
    * @method setColor
    * @param {Color} color
    */
    setColor(color)
    {
        this.color = color;
    }
    /**
    * Defines the letter's x
    * @method setX
    * @param {Number} x
    */
    setX(val)
    {
        this.x = val;
    }
    /**
    * Defines the letter's y
    * @method setY
    * @param {Number} y
    */
    setY(val)
    {
        this.y = val;
    }
    /**
    * Defines the letter's font size
    * @method setSize
    * @param {Font} size
    */
    setSize(val)
    {
        this.size = val;
    }
    /**
    * Defines the letter's path
    * @method setPath
    * @param {Path} openType.js object's Path
    */
    setPath(val)
    {
        this.path = val;
    }
    /**
    * Defines the letter's text string
    * @method setText
    * @param {String} text
    */
    setText(val)
    {
        this.text = val;
        this.styledLetters = [];

        for(let i=0; i<this.text.length; i++)
        {
            let char = this.text.charCodeAt(i);
            let styledLetter = new StyledLetter({letter: String.fromCharCode(char),
                                                 font: this.font,
                                                 size: this.size,
                                                 color: this.color
                                                });
            this.styledLetters.push(styledLetter);
        }
    }
}

class TextLine
{
    /**
    * TextLine is a thin layer class to organise text elements extacted from HTML text in lines. It gives the possibility to easily compute the global size of the line. Used in RichText.
    * @class TextLine
    * @constructor
    */
    constructor()
    {
        this.styledTextElements = [];
        this.width = 0;
    }
}

export { StyledLetter, StyledTextElement, TextLine };
