import {getOrDefault} from '../helpers/Misc';

class Font
{
    /**
     Font are not managed in Mobilizing.js like in a usual DOM context as we are using WebGL. To be able to use both bitmap rendered font and 3D vector rendering, we use openType.js (https://nodebox.github.io/opentype.js/ big thanks to Frederik!) to load any web compilant font and the use it in other classes.
     @class Font
     @constructor
     @param {Object} params
     @param {Mixed} params.fontFile the font file already loaded, must be loaded with response type setted to arraybuffer
    */
    constructor(params)
    {
        this._fontFile = getOrDefault(params, "fontFile", undefined);
        this._font = opentype.parse(this._fontFile);
    }

    /**
    * Return the underlying Font.js object to work with it outside of Mobilizing.Font
    * @private
    * @method getFont
    * @return {Object} Font.js object
    */
    getFont()
    {
        return this._font;
    }

    /**
    * Calculates the size (in pixels) that should take the string with this font at the given fontSize. Handy for Label creation.
    * @method getTextSize
    * @param {String} text the string to use for the size computing
    * @param {Number} fontSize the text font size to use
    * @return {Object.width, Object.height} width and height
    */
    getTextSize(text, fontSize)
    {
        if (!this._font.supported)
        {
            return null;
        }

        let kerning, fontScale, glyphs, i, glyph, options, kerningValue;
        let ymax = [];
        let x = 0;

        fontSize = fontSize !== undefined ? fontSize : 72;
        options = options || {};
        kerning = options.kerning === undefined ? true : options.kerning;
        fontScale = 1 / this._font.unitsPerEm * fontSize;
        glyphs = this._font.stringToGlyphs(text);

        for (i = 0; i < glyphs.length; i += 1)
        {
            glyph = glyphs[i];

            ymax.push( (glyph.getMetrics().yMax - glyph.getMetrics().yMin) * fontScale);
            if (glyph.advanceWidth)
            {
                x += glyph.advanceWidth * fontScale;
            }
            if (kerning && i < glyphs.length - 1)
            {
                kerningValue = this._font.getKerningValue(glyph, glyphs[i + 1]);
                x += kerningValue * fontScale;
            }
        }
        return({width: x, height: Math.max.apply(0,ymax) });
    }
}

// module exports
export { Font };
