import {Texture} from './Texture';
import {debug} from '../helpers/Debug';

/**
 * The ImageSequenceTexture represents a special kind of 2D Texture that is built from a sequence of pictures that you can play at a given rate and with a given play mode.
 *
 * @class ImageSequenceTexture
 */

class ImageSequenceTexture
{
    //var currentTime     = 0;
    //var playing        = false;

    /**
    @method constructor
    @param {Object} context The Mobilizing Context.
    */
    constructor(context)
    {
        this.currentTime    = 0;
        this.playing        = false;
        this.frameRate      = 10;
        this.context        = context;
        this.textures       = [];
        //
        this.sequenceMode   = 'FORWARD';
        this.direction      = 1;
        this.lapsToDo       = -1;
        this.frameCount     = 0;
        this.laps           = 0;
        this.currentFrame   = 0;

    }

    /**
    Load an image sequence from a wild card URL (the * sign will be replaced by an index starting from min and ending at max).

    @method load
    @param {String} urlbase wild card base URL. This can be a relative or absolute URL, and can be local or online, depending on your Browser cross domain policies.
    @param {Integer} min starting index.
    @param {Integer} max ending index.
    */
    load(urlbase, min, max)
    {
        for (var i=min;i<=max;++i)
        {
            var t = new Texture({context:this.context});
            var file = urlbase.replace("*", ""+i+"");
            t.loadFromFile(file, true);
            this.textures.push(t);
        }
        this.frameCount = this.textures.count;
        this.enterFrame = 0;
        this.currentFrame = 0;
        this.outFrame = max;
    }

    /**
    Set the starting frame of the current loaded sequence.
    @method setEnterFrame
    @param {Integer} frame starting frame.
    */
    setEnterFrame(frame)
    {
        if(frame >=0 && frame < this.frameCount){
            this.enterFrame = frame;
            this.currentFrame = this.enterFrame;
        }else{
            this.enterFrame = 0;
            this.currentFrame = 0;
            debug.log("ERROR enterFrame %i is out of sequence bounds - back to 0",frame);
        }
    }

    /**
    Set the ending frame of the current loaded sequence.
    @method setOutFrame
    @param {Integer} frame ending frame.
    */
    setOutFrame(frame)
    {
        if(frame >=this.enterFrame && frame < this.frameCount)
        {
            this.outFrame = frame;
        }
        else
        {
            this.outFrame = this.frameCount-1;
            debug.log("ERROR outFrame %i is out of sequence bounds - back to frameCount",frame);
        }
    }

    /**
    Set the sequence playing mode.
    @method setMode
    @param {String} mode playing mode : "FORWARD", BACKWARD", "PINGPONG", "RANDOM".
    */
    setMode(mode)
    {
        this.sequenceMode = mode;
    }

    /**
    update the Image sequence. You have to call this once per frame for the Image sequence Texture to update and run its own logic.
    @method update
    */
    update()
    {
        if (!this.playing)
        {
            return;
        }
        this.currentTime += this.context.Time.deltaTime;

        if (this.currentTime > 1/this.frameRate)
        {
            this.currentTime -= 1/this.frameRate;
            //this.currentFrame = Math.floor(this.currentTime*this.frameRate) % this.textures.length;
            if(this.sequenceMode === 'FORWARD')
            {
                if(this.laps >= 0 && this.laps < this.lapsToDo)
                {
                    this.currentFrame++;
                    this.currentFrame %= this.outFrame+1;

                    if(this.currentFrame === 0)
                    {
                        this.currentFrame = this.enterFrame;
                    }

                    if(this.currentFrame === this.outFrame)
                    {
                        this.laps++;
                    }
                }
                else if(this.lapsToDo < 0)
                {
                    this.currentFrame++;
                    this.currentFrame %= this.outFrame+1;
                    if(this.currentFrame === 0)
                    {
                        this.currentFrame = this.enterFrame;
                    }
                }
                if(this.lapsToDo === this.laps)
                {
                    this.stop();
                }
                debug.log("currentFrame = " + this.currentFrame);
            }
            else if(this.sequenceMode === 'BACKWARD')
            {
                if(this.laps >= 0 && this.laps < this.lapsToDo)
                {
                    this.currentFrame--;

                    if(this.currentFrame < this.enterFrame)
                    {
                        this.currentFrame = this.outFrame;
                    }
                    if(this.currentFrame === this.enterFrame)
                    {
                        this.laps++;
                    }

                }
                else if(this.lapsToDo < 0)
                {
                    this.currentFrame--;

                    if(this.currentFrame < this.enterFrame)
                    {
                        this.currentFrame = this.outFrame;
                    }
                }

                if(this.lapsToDo === this.laps)
                {
                    this.stop();
                }
                debug.log("currentFrame = " + this.currentFrame);
            }
            else if(this.sequenceMode === 'PINGPONG')
            {

                if(this.laps >= 0 && this.laps < this.lapsToDo)
                {
                    this.currentFrame += this.direction;

                    //protection against border over
                    if(this.currentFrame < 0)
                    {
                        this.currentFrame = 0;
                    }
                    if(this.currentFrame > this.outFrame)
                    {
                        this.currentFrame = this.outFrame;
                    }

                    if(this.currentFrame >= this.outFrame)
                    {
                        this.direction = -this.direction;
                        this.laps++;
                    }
                    if(this.currentFrame <= this.enterFrame)
                    {
                        this.direction = -this.direction;
                        this.laps++;
                    }
                }
                else if(this.lapsToDo < 0)
                {
                    this.currentFrame += this.direction;
                    if(this.currentFrame >= this.outFrame)
                    {
                        this.direction = -this.direction;
                    }
                    if(this.currentFrame <= this.enterFrame)
                    {
                        this.direction = -this.direction;
                    }
                }

                if(this.lapsToDo === this.laps)
                {
                    this.stop();
                }
                debug.log("currentFrame = " + this.currentFrame);
            }
            else if(this.sequenceMode === 'RANDOM')
            {
                this.currentFrame = Math.floor(Math.random()*(this.outFrame-this.enterFrame)+this.enterFrame);
                debug.log("currentFrame = " + this.currentFrame);
            }
        }
    }

    /**
    Get the current Image Sequence frame Texture.
    @method getCurrentTexture
    @return {Texture} the texture
    */
    getCurrentTexture()
    {
        return this.textures[this.currentFrame];
    }

    /**
    play the Image Sequence.
    @method play
    */
    play()
    {
        this.playing = true;
    }

    /**
    pause the Image Sequence.
    @method pause
    */
    pause()
    {
        this.playing = false;
    }

    /**
    stop the Image Sequence. The image frame is resetted.
    @method stop
    */
    stop()
    {
        this.currentTime    = 0;
        this.currentFrame   = 0;
        this.playing        = false;
        this.laps           = 0;
    }

    /**
    Loop the image sequence for a given number of iterations (once play() is called).
    @method loop
    @param {Number} count : -1 means infinity.
    */
    loop(count)
    {
        this.lapsToDo = count;
    }


}

// module exports
export { ImageSequenceTexture };
