import {Mesh} from '../shape/Mesh';
import {Material} from '../scene/Material';
import {Vector2} from '../core/Vector2';
import {Vector3} from '../core/Vector3';
import {debug} from '../helpers/Debug';

/**
 * OBJ class gives easy access to some calculations specific to OBJs. It is used to load an external .obj model file.
 *
 * @class OBJ
 */

class OBJ
{
    /**
     * Load an OBJ and dependencies from a URL
     * @method load
     * @static
     * @param {Loader} loader
     * @param {Object} params The parameters object
     * @param {Function} [params.onStart = null] A function to be called when the loading starts
     */
    static load(loader, params)
    {
        OBJ.callBack = params.onLoad;
        return loader.loadText({
            url:params.url,
            onLoad:OBJ.parseOBJ.bind(null,loader)
        });
    }

    /**
     * Attach a texture to the given model
     * @method attachTexture
     * @static
     * @param {Loader} loader
     * @param {Object} model The model to attach a texture to
     * @param {Texture} texture The Texture object to attach
     */
    static attachTexture(loader, model, texture)
    {
        debug.log("attachTexture ", loader, model, texture);
        model.material.setTexture(texture);
    }

    /**
     * parse the MTL file associated to the obj file
     * @method parseMTL
     * @static
     * @param {Loader} loader
     * @param {Object} model The model to attach a texture to
     * @param {Object} data The data to parse
     */
    static parseMTL(loader, model, data)
    {
        debug.log("model=" + model);
        //OBJ.mtl = data;
        debug.log("mtl content =", data);
        let lines = data.toString().split("\n");
        //debug.log("MTL lines = " + lines);
        lines.forEach(line => {
            let words = line.split(" ");
            //debug.log("WORDS:"+words);
            switch(words[0])
            {
                    //comment, we just ignore
                case "#":
                    break;
                    //new material declared
                case "newmtl":
                    debug.log("new material : " + words[1]);
                    break;
                    //ambient term
                case "Ka":
                    model.material.setEmissiveColor(new Mobilizing.Color(words[1],words[2],words[3]));
                    break;
                case "Kd": //diffuse
                    model.material.setColor(new Mobilizing.Color(words[1],words[2],words[3]));
                    break;

                case "Ks": //specular
                    model.material.setSpecularColor(new Mobilizing.Color(words[1],words[2],words[3]));
                    model.material._material.shininess = 20;
                    break;

                case "Ns": //specular exponent

                    break;

                case "d": //dissolved == opacity

                    break;

                case "Tr": //Transparent
                    //d = 1-Tr;
                    break;

                case "illum": //illumination model

                    break;

                case "map_Kd": //Map diffuse
                    //load texture
                    //debug.log("load texture : " + words[1]);
                    loader.loadTexture({
                        url:    words[1],
                        onLoad: OBJ.attachTexture.bind(null, loader, model)
                    });

                    model.updateMaterial();
                    break;
                default:
                    debug.log("unknown MTL line : " , line);
                    break;
            }
        });
        //model.material.setColor(Mobilizing.Color.green);
    }

    /**
     * parse the OBJ file
     * @method parseOBJ
     * @static
     * @param {Loader} loader
     * @param {Object} data The data to parse
     */
    static parseOBJ(loader, data)
    {
        debug.log("onLoaded data=", data);
        debug.log("loader=", loader);
        //debug.log("onLoaded result=", this.result);
        //this.text = data.getResponseText();
        OBJ.text = data;
        debug.log("text=", OBJ.text);
        //this.text = this.result._value; //FIXME

        let p;
        if (OBJ.mesh !== undefined)
        {
            p = OBJ.mesh;
        }
        else
        {
            debug.log("create material");
            p = new Mesh({});
            p.clear();//empty the current mesh to have clean buffers

            p.material = new Material({type:"phong"});
            debug.log("Material created : " , p.material);
        }

        debug.log("parsing obj file...");

        //OBJ parsing =====================================
        let lines = OBJ.text.toString().split("\n");
        let uvs = [];
        let normals = [];

        lines.forEach(line => {
            let words = line.split(" ");

            //debug.log("LINE " + line);
            switch(words[0])
            {
                case "mtllib": //chargement de la lib
                    debug.log("mtllib " + words[1]);
                    OBJ.resultmtl = loader.loadText({
                        url:words[1],
                        onLoad:OBJ.parseMTL.bind(null,loader, p)
                    });
                    break;

                case "usemtl":
                    debug.log("usemtl " + words[1]);
                    //to do : load mtl file

                    break;

                case "o":
                    debug.log("OBJ o " + words[1]);
                    break;

                case "g":
                    debug.log("OBJ g " + words[1]);
                    break;

                case "v":
                    //debug.log("Vertice")
                    p.pushVertex(new Vector3(words[1], words[2], words[3]));
                    break;

                case "vt":
                    debug.log("uv " + words[1] + " " + words[2]);
                    uvs.push(new Vector2(words[1], words[2]));
                    break;

                case "vn":
                    normals.push(new Vector3(words[1], words[2], words[3]));
                    //debug.log("normal")
                    break;

                case "s":
                    //debug.log("Face")
                    break;

                case "f": {//FACE definition
                    //debug.log(uvs);
                    //debug.log("Face")
                    let index1 = words[1].split("/");
                    let index2 = words[2].split("/");
                    let index3 = words[3].split("/");

                    p.pushFace(index1[0]-1, index2[0]-1, index3[0]-1);
                    p.pushUV(uvs[index1[1]-1], uvs[index2[1]-1], uvs[index3[1]-1]);

                    //debug.log(index1, index2, index3);
                    //p.pushNormal(normals[index1[2]-1], normals[index2[2]-1], normals[index3[2]-1]); //NEW
                    //f.normal = new Vector3(normals[index1[2]-1], normals[index2[2]-1], normals[index3[2]-1]);

                    if (words.length > 4)
                    {
                        //we have quads, we need to add an additionnal triangle
                        let index4 = words[4].split("/");
                        p.pushFace(index1[0]-1, index3[0]-1, index4[0]-1);
                        p.pushUV(uvs[index1[1]-1], uvs[index3[1]-1], uvs[index4[1]-1]);
                    }
                    break;
                }
                case "#":
                    //debug.log("Comment")
                    break;
            }
        });

        p.updateMesh();
        p.computeNormals();
        //p.updateMaterial();
        debug.log("callback called : ", p);
        OBJ.callBack(p);
        //p.material.setTexture(OBJ.texture);
    }
}

// module exports
export { OBJ };
