class Rect
{
    /**
    * Rect hold a simple rectangle representation with x & y coordinates and width & height.
    *
    * @class Rect
    * @constructor
    * @param {float} x
    * @param {float} y
    * @param {float} width
    * @param {float} height
    */
    constructor(x,y, width,height)
    {
        this.x      = x;
        this.y      = y;
        this.width  = width;
        this.height = height;
    }

    /**
    * Return an array representation
    * @method toArray
    * @return {Array} [x, y, width, height]
    */
    toArray()
    {
        return [
            this.x,
            this.y,
            this.width,
            this.height
        ];
    }

    /**
    * Sets the rect's value based on an array formatted like [x, y, width, height]
    * @method fromArray
    * @param {Array} array [x, y, width, height]
    */
    fromArray(array)
    {
        this.x = array[0];
        this.y = array[1];
        this.width = array[2];
        this.height = array[3];
    }

}

// module exports
export { Rect };
