import * as THREE from 'three';

import {_DOM} from '../helpers/DOM';
import {debug} from '../helpers/Debug';
import {Component} from '../Component';
import {singletonize} from '../helpers/Misc';
import {getOrDefault} from '../helpers/Misc';
import {Color} from "../core/Color";

class Renderer3D extends Component
{
    /**
     * Renderer3D is a Three js based renderer.
     *
     *
     * @example
     *     //to do
     *
     * @class Renderer3D
     * @constructor
     * @extends Component
     * @param {Object} params Config parameters
     * @param {Context} params.context The Mobilizing context
    */
    constructor(params)
    {
        super(params);
        this.canvas = getOrDefault(params, "canvas", undefined);
        this.scenes = [];
        this.cameras = [];
        this.fog = undefined;
        this.setCurrentScene("default");
        this.engine = new Mobilizing.Engine3D({canvas: this.canvas}); //3D engine creation - we pass the Canvas

        //we must register the canvas when made internally by Mobilizing/Three
        if(this.canvas)
        {
            this.fullscreen = false;
            debug.log("got canvas from runner to context", this.canvas);
        }
        else
        {
            this.canvas = this.engine.canvas;
            this.fullscreen = true;
            debug.log("got canvas from Engine", this.canvas);
        }

        //==================================================================

        //window management
        window.addEventListener("orientationchange", event => this.onWindowResize(event), false);
        window.addEventListener("resize", event => this.onWindowResize(event), false );

        // deal with the page getting resized or scrolled
        window.addEventListener("scroll", event => this.updateCanvasPosition(event), false);
        window.addEventListener("resize", event => this.updateCanvasPosition(event), false);

        this.canvasPosition = this.getCanvasPosition();
    }

    /*setup()
    {
    }*/

    /*on(){
        super.on();
    }

    off(){
        super.off();
    }*/

    /**
     * @method update
     */
    update()
    {
        this.render();
    }

    //deprecated
    setClearColor(color, alpha)
    {
        this.engine.renderer.setClearColor(color,alpha);
    }

    /**
    * Activates the rendering of projected shadows
    * @method setEnableShadowMap
    * @param {Boolean} state
    */
    setEnableShadowMap(state)
    {
        this.engine.renderer.shadowMap.type = THREE.PCFSoftShadowMap;
        this.engine.renderer.shadowMapEnabled = state;
    }

    /**
     * Tells the context to use this scene (defined by a string) or create it and switch to it.
     * @method setCurrentScene
     * @param {String} name
     */
    setCurrentScene(name)
    {
        this.scene = this.scenes[name];

        if (this.scene === undefined)
        {
            this.scene = new THREE.Scene();
            this.scenes[name] = this.scene;
            debug.log("current scene is : ", this.scene);
        }
    }

    /**
     * Adds on object to the current scene
     * @method addToCurrentScene
     * @param {Object} object A Mesh or Light to add the scene
     */
    addToCurrentScene(object)
    {
        this.scene.add(object.transform.entity);
    }

    /**
     * Remove from the current scene
     * @method removeFromCurrentScene
     * @param {Object} object the object to remove
     */
    removeFromCurrentScene(object)
    {
        this.scene.remove(object.transform.entity);
    }

    /**
     * Clears this renderer : remove all cameras and scene objects
     *
     * @method addCamera
     * @param {Camera} cam the camera to add
     */
    clear()
    {
        console.log("scene=" , this.scene);

        this.scene.children.forEach((child) => {
            console.log("removing " , child);
            this.scene.remove(child);
        });

        this.scene.children = [];

        this.cameras = [];
    }

    /**
     * Adds a camera to the current context
     *
     * @method addCamera
     * @param {Camera} cam the camera to add
     */
    addCamera(cam)
    {
        this.cameras.push(cam);
    }

    /**
     * Removes the camera from the current context
     *
     * @method removeCamera
     * @param {Camera} cam
     */
    removeCamera(cam)
    {
        this.cameras.splice( this.cameras.indexOf(cam), 1 );
    }

    /**
     * Defines the type and color of the fog to be used for rendering the scene
     *
     * @method setFog
     * @param {String} type one of "linear", "exp"
     * @param {Color} color the fog color
     */
    setFog(type, color)
    {
        if(!color)
        {
            color = Color.black.clone();
        }

        switch(type)
        {
            case "linear" :
                this.fog = new THREE.Fog(color.getHex());
                this.fog.type = "linear";
                break;

            case "exp" :
                this.fog = new THREE.FogExp2(color.getHex());
                this.fog.type = "exp";
                break;

            default :
                this.fog = new THREE.Fog(color.getHex());
                this.fog.type = "linear";
                break;
        }
        this.scene.fog = this.fog;
    }

    /**
     * Defines the near distance of the linear fog. Default is 1.
     *
     * @method setFogNear
     * @param {Number} near
     */
    setFogNear(near)
    {
        if(this.fog.type === "linear")
        {
            this.fog.near = near;
        }
    }

    /**
     * Defines the far distance of the linear fog. Default is 1000.
     *
     * @method setFogFar
     * @param {Number} far
     */
    setFogFar(far)
    {
        if(this.fog.type === "linear")
        {
            this.fog.far = far;
        }
    }

    /**
     * Defines the density of the exponential fog.  Default is 0.00025.
     *
     * @method setFogDensity
     * @param {Number} density
     */
    setFogDensity(val)
    {
        if(this.fog.type === "exp")
        {
            this.fog.density = val;
        }
    }

    /**
     * Defines the fog color
     *
     * @method setFogColor
     * @param {Color} color
     */
    setFogColor(color)
    {
        this.fog.color = color;
    }


    onWindowResize()
    {
        if(this.fullscreen)
        {
            this.cameras.forEach( cam => {

                if(cam.type === "perspective"){

                    var w = this.engine.renderer.domElement.width/this.engine.renderer.devicePixelRatio;
                    var h = this.engine.renderer.domElement.height/this.engine.renderer.devicePixelRatio;
                    cam.setAspect((w*cam.viewport.width) / (h*cam.viewport.height));

                }else if(cam.type === "ortho"){

                    cam.setOrthoPlanes(window.innerWidth/-2,
                                       window.innerWidth/2,
                                       window.innerHeight/2,
                                       window.innerHeight/-2);
                }
            });
            this.engine.renderer.setSize( window.innerWidth, window.innerHeight);
        }
    }

    /**
     * Renders all the camera in the context
     * @method render
     */
    render()
    {
        //rendu de toutes les camÃ©ras
        //debug.log("render " + this.cameras.length + " cams");
        //simple insertion index sorting
        for (var l=0; l<this.cameras.length; ++l)
        {
            this.cameras[l].sort = false;
            if (this.cameras[l].index === undefined)
            {
                this.cameras[l].index = 0;
            }
        }

        var sorted_cameras = [];
        var min_val = 9999;
        var min_cam = null;

        for (var i=0;i<this.cameras.length; ++i)
        {
            //recherche de l'Ã©lÃ©ment le plus petit pas encore triÃ©
            min_val = 9999;
            min_cam = null;
            for (var j=0;j<this.cameras.length; ++j)
            {
                var cam_candidate = this.cameras[j];
                //debug.log("evaluating cam.index=" + cam_candidate.index);
                if (cam_candidate.index <= min_val && !cam_candidate.sort)
                {
                    min_cam = cam_candidate;
                    min_val = cam_candidate.index;
                }
            }

            if(min_cam)
            {
                min_cam.sort = true;
                //debug.log("push cam with index=" + min_val);
                sorted_cameras.push(min_cam);
            }
        }

        for (var k=0; k<sorted_cameras.length; ++k)
        {
            //debug.log("render " + k);
            var cam = sorted_cameras[k];

            if (cam.transform.getVisible() && (cam.dirty||cam.autorender))
            {
                var w = this.engine.renderer.domElement.width / this.engine.renderer.devicePixelRatio;
                var h = this.engine.renderer.domElement.height /this.engine.renderer.devicePixelRatio;
                if (cam.type !== "cube")
                {
                    cam.setAspect((w * cam.viewport.width) / (h * cam.viewport.height));
                }
                var viewport_x = w*cam.viewport.x;
                var viewport_y = h*cam.viewport.y;
                var viewport_w = w*cam.viewport.width;
                var viewport_h = h*cam.viewport.height;
                this.engine.renderer.setViewport(viewport_x, viewport_y, viewport_w, viewport_h);
                this.engine.renderer.setScissor(viewport_x, viewport_y, viewport_w, viewport_h);
                this.engine.renderer.setScissorTest(true);
                this.engine.renderer.autoClear = cam.autoclear;

                for (var ks in this.scenes)
                {
                    if (this.logLevel === 1)
                    {
                        debug.log("render cam/scene ", cam, ks);
                    }
                    if (cam.layer === ks) //si la scÃ¨ne a le mÃªme nom que le layer de la camÃ©ra
                    {
                        if (cam.renderTexture !== undefined)
                        {
                            this.engine.renderer.render(this.scenes[ks], cam._camera, cam.renderTexture._engineTexture, cam.autoClear);
                        }
                        else if (cam.type === "cube")
                        {
                            cam._camera.updateCubeMap( this.engine.renderer, this.scenes[ks] );
                        }
                        else
                        {
                            this.engine.renderer.render(this.scenes[ks], cam._camera);
                        }
                        cam.dirty = false;
                    }
                }
            }
        }
    }

    /**
    * Returns the current canvas
    *
    * @method getCanvas
    * @return {Object} the canvas
    */
    getCanvas()
    {
        return this.canvas;
    }

    /**
    * Returns the current canvas'size as {width, height}
    *
    * @method getCanvasSize
    * @return {Object} the size of the canvas as {width, height}
    */
    getCanvasSize()
    {
        return { width: this.canvas.width/window.devicePixelRatio,
                height: this.canvas.height/window.devicePixelRatio};
    }

    /**
    * Get the canvas position as {x, y}
    *
    * @method getCanvasPosition
    * @return {Object} the position of the canvas as {x, y}
    */
    getCanvasPosition()
    {
        return _DOM.getElementPosition(this.canvas);
    }

    /**
    * Update the canvas position in screen
    *
    * @method updateCanvasPosition
    */
    updateCanvasPosition()
    {
        this.canvasPosition = this.getCanvasPosition();
    }
}


const Renderer3DSingleton = singletonize(Renderer3D);

// module exports
export { Renderer3DSingleton as Renderer3D };
