import * as THREE from 'three';
import {getOrDefault, getOrExec} from '../helpers/Misc';
import {debug} from '../helpers/Debug';

class CanvasTexture
{
    /**
     * CanvasTexture Class simplifies the creation of texture from an HTML5 canvas. Any valid canvas can be used to create a texure that can then be attached to a material.
     * Some helpers static methods are implemented to quickly creates some commons shapes (rectangles, circle).
     * Stroke and fill colors can be changed on the fly if update methods is called afterward
     *
     * @class CanvasTexture
     * @constructor
     * @param {Object} params an object containing all the possible parameters for this class. Use it like in the following example. Possible parameters are lisited below.
     * @example
     *     //TODO
     *
     * @param {Canvas} [params.canvas = a canvas will be created internally if undefined] the canvas to use
     * @param {Context} [params.context] the Mobilizing context to use
     * @param {Number} [params.width = 100] width the width of the canvas
     * @param {Number} [params.height = 100] height the height of the canvas
     * @param {Number} [params.strokeWidth = 1] strokeWidth as an integer
     * @param {Color} [params.strokeColor] strokeColor as a Mobilizing Color
     * @param {Number} [params.strokeOpacity = 1] strokeOpacity as a float between 0 and 1
     * @param {Color} [params.fillColor] fillColor as a Mobilizing Color
     * @param {Number} [params.fillOpacity = 1] fillOpacity as a float between 0 and 1
    */
    constructor(params)
    {
        this._strokeWidth = getOrDefault(params, "strokeWidth", 1);
        this._strokeColor = getOrDefault(params, "strokeColor", undefined);
        this._strokeOpacity = getOrDefault(params, "strokeOpacity", 1);
        this._fillColor = getOrDefault(params, "fillColor", undefined);
        this._fillOpacity = getOrDefault(params, "fillOpacity", 1);

        this._width = getOrDefault(params, "width", 100);
        this._height = getOrDefault(params, "height", 100);

        this._canvas = getOrExec(params, "canvas", () => {
            let canvas = document.createElement("canvas");
            canvas.width = this._width;
            canvas.height = this._height;
            return canvas;
        });

        this._texture = new THREE.Texture(this._canvas);
        this._texture.needsUpdate = true;
        //avoid smoothing of pixels
        this._texture.minFilter = THREE.NearestFilter;
        this._texture.magFilter = THREE.NearestFilter;
        this._texture.generateMipmaps = false;

        this._canvasContext = this._canvas.getContext("2d");

        this.update();
    }

    /**
    * Get this texture base canvas
    * @method getCanvas
    * @return canvas {Canvas}
    */
    getCanvas()
    {
        return this._canvas;
    }

    /**
    * Get this texture base canvas context, to directly daw on it
    * @method getCanvasContext
    * @return canvasContext {CanvasContext}
    */
    getCanvasContext()
    {
        return this._canvasContext;
    }

    /**
    * Fills the canvas with the given color
    * @method fillWithColor
    * @param {Color} color Mobilizing Color to use
    */
    fillWithColor(color)
    {
        this.setFillColor(color);
        this.update();
        this._canvasContext.fillRect(0,0, this._width, this._height);
        this._texture.needsUpdate = true;
    }

    /**
    * resizeCanvas resizes and refresh the canvasContext interally
    * @method resizeCanvas
    * @param {Number} width
    * @param {Number} height
    */
    resizeCanvas(width, height)
    {
        this._canvas.width = width;
        this._canvas.height = height;
        this._canvasContext = this._canvas.getContext("2d");
    }

    /**
    * Get the canvas's width
    * @method getWidth
    * @return width {Number}
    */
    getWidth()
    {
        return this._width;
    }

    /**
    * Get the canvas's height
    * @method getHeight
    * @return height {Number}
    */
    getHeight()
    {
        return this._height;
    }

    /**
    * @method setStrokeColor
    * @param {Color} c new stroke width
    */
    setStrokeColor(c)
    {
        this._strokeColor = c;
    }

    /**
    * @method setFillColor
    * @param {Color} c new stroke width
    */
    setFillColor(c)
    {
        this._fillColor = c;
    }

    /**
    * @method setStrokeWidth
    * @param {Number} w new stroke width
    */
    setStrokeWidth(w)
    {
        this._strokeWidth = w;
    }

    /**
    * Updates the current canvas so that it reflects graphically all the changes done (colors, strokeWidth, etc.)
    * Should be called after the changes has been given.
    * @method update
    */
    update()
    {
        var tempStrokeColor, tempFillColor;

        if(this._strokeColor)
        {
            tempStrokeColor = this.toRGBAColorString(this._strokeColor, this._strokeOpacity);
        }
        else
        {
            tempStrokeColor = "rgba(0,0,0,0)";
        }
        if(this._fillColor)
        {
            tempFillColor = this.toRGBAColorString(this._fillColor, this._fillOpacity);
        }
        else
        {
            tempFillColor = "rgba(0,0,0,0)";
        }

        debug.log("this._strokeColor",this._strokeColor,"this._fillColor",this._fillColor);
        debug.log("tempStrokeColor",tempStrokeColor,"tempFillColor",tempFillColor);

        this._canvasContext.strokeStyle = tempStrokeColor;
        this._canvasContext.fillStyle = tempFillColor;
        this._canvasContext.lineWidth = this._strokeWidth;

        this._canvasContext.clearRect(0,0,this._width, this._height);
        this._canvasContext.fill();
        this._canvasContext.stroke();

        this._texture.needsUpdate = true;
    }

    /**
    * Draw an Mobilizing StyledText or StyledLetter in the canvas object and updates it.
    *
    * @method drawStyledText
    * @param {StyledText || StyledLetter} text
    */
    drawStyledText(text)
    {
        text.path.draw(this._canvasContext);
        this._texture.needsUpdate = true;
    }

    /**
    * Draw an Image in the canvas object and updates it.
    *
    * @method drawImage
    * @param {Image} image
    * @param {Number} x x coordinate to start drawing (from upper-left corner)
    * @param {Number} y y coordinate to start drawing (from upper-left corner)
    * @param {Number} [width=canvas.width] the destination width to draw to
    * @param {Number} [height=canvas.height]  the destination height to draw to
    */
    drawImage(image, x, y, width, height)
    {
        this._canvasContext.drawImage(image,
                                      (x) ? x : 0,
                                      (y) ? y : 0,
                                      (width) ? width : this._width,
                                      (height) ? height : this._height);

        this._texture.needsUpdate = true;
    }

    /**
    * toRGBAColorString makes a css rgba color from a Mobilizing Color and an alpha value (0:1)
    * @private
    * @method toRGBAColorString
    * @param {Color} color
    * @param {Number} alpha
    */
    toRGBAColorString(color,alpha)
    {
        var c = "rgba("+Math.floor(color.r*255)+","+Math.floor(color.g*255)+","+Math.floor(color.b*255)+","+ alpha +")";
        return c;
    }

    /**
    * CreateRect prepares a canvas and a path to draw a rectangle
    * @static
    * @method CreateRect
    * @param {Object} params object of paramaters
    * @param {Number} params.width
    * @param {Number} params.height
    * @param {Number} params.strokeWidth
    * @param {Color} params.strokeColor
    * @param {Color} params.fillColor
    */
    static CreateRect(params)
    {
        let width = getOrDefault(params, "width", 100);
        let height = getOrDefault(params, "height", 100);
        let strokeWidth = getOrDefault(params, "strokeWidth", 1);
        let strokeColor = getOrDefault(params, "strokeColor", 0);
        let fillColor = getOrDefault(params, "fillColor", 0);
        let fillOpacity = getOrDefault(params, "fillOpacity", 1);
        let strokeOpacity = getOrDefault(params, "strokeOpacity", 1);

        let canvas = document.createElement("canvas");
        canvas.width = width; canvas.height = height;
        let ctx = canvas.getContext("2d");
        ctx.lineWidth = strokeWidth;
        ctx.beginPath();
        ctx.lineTo(0,0);
        ctx.lineTo(width,0);
        ctx.lineTo(width,height);
        ctx.lineTo(0,height);
        ctx.lineTo(0,0);
        ctx.closePath();

        ctx.fill();
        ctx.stroke();

        //reset params
        params = {};

        params.canvas = canvas;
        params.width = width;
        params.height = height;
        params.strokeWidth = strokeWidth;
        params.strokeColor = strokeColor;
        params.fillColor = fillColor;
        params.fillOpacity = fillOpacity;
        params.strokeOpacity = strokeOpacity;

        let obj = new CanvasTexture(params);

        return obj;
    }

    /**
    * CreateCircle prepares a canvas and a path to draw a circle
    * @static
    * @method CreateCircle
    * @param {Object} params object of paramaters
    * @param {Number} params.radius
    * @param {Number} params.strokeWidth
    * @param {Color} params.strokeColor
    * @param {Color} params.fillColor
    */
    static CreateCircle(params)
    {
        var radius = getOrDefault(params, "radius", 100);
        var strokeWidth = getOrDefault(params, "strokeWidth", 1);
        var strokeColor = getOrDefault(params, "strokeColor", 0);
        var fillColor = getOrDefault(params, "fillColor", 0);
        var fillOpacity = getOrDefault(params, "fillOpacity", 1);
        var strokeOpacity = getOrDefault(params, "strokeOpacity", 1);

        var canvas = document.createElement("canvas");
        canvas.width = radius+strokeWidth; canvas.height = radius+strokeWidth;
        var ctx = canvas.getContext("2d");
        ctx.strokeStyle = strokeColor ? strokeColor.getHexString() : undefined;
        ctx.fillStyle = fillColor ? fillColor.getHexString() : undefined;
        ctx.lineWidth = strokeWidth;
        //ctx.rect(0,0,canvas.width,canvas.height);
        ctx.beginPath();
        ctx.arc(radius/2 + strokeWidth/2, radius/2 + strokeWidth/2, radius/2, 0, 2 * Math.PI, false);
        ctx.closePath();

        ctx.fill();
        ctx.stroke();

        document.body.appendChild(canvas);

        //reset params
        params = {};

        params.canvas = canvas;
        params.width = radius;
        params.height = radius;
        params.strokeWidth = strokeWidth;
        params.strokeColor = strokeColor;
        params.fillColor = fillColor;
        params.fillOpacity = fillOpacity;
        params.strokeOpacity = strokeOpacity;

        var obj = new CanvasTexture(params);

        return obj;
    }

}

// module exports
export { CanvasTexture };
