import {Component} from '../Component';
import {getOrDefault} from './Misc';
import {Mouse} from '../input/Mouse';
import {Touch} from '../input/Touch';

var TOUCH_TYPE = "touch";
var MOUSE_TYPE = "mouse";

class PointerObject
{
    /**
    * PointerObject is a thin layer that unify all inputs to simplify UI interactions
    *
    * @class PointerObject
    * @constructor
    * @param {Number} x
    * @param {Number} y
    * @param {Number} state
    * @param {Number} type
    */
    constructor(params)
    {
        let x = getOrDefault(params, "x", 0);
        let y = getOrDefault(params, "y", 0);
        let state = getOrDefault(params, "state", false);
        let type = getOrDefault(params, "type", undefined);

        this._x = x;
        this._y = y;
        this._deltaX = 0;
        this._deltaY = 0;
        this._state = state;
        this._type = type;
        this._component = null;
    }
    /**
    * Set the component
    *
    * @method setComponent
    * @param {Object} component
    */
    setComponent(component)
    {
        this._component = component;
    }
    /**
    * get the component
    *
    * @method getComponent
    * @return {Object} component
    */
    getComponent()
    {
        return this._component;
    }
    /**
    * Set the x coordinate
    *
    * @method setX
    * @param {Object} x
    */
    setX(x)
    {
        this._x = x;
    }
    /**
    * Set the y coordinate
    *
    * @method setY
    * @param {Object} y
    */
    setY(y)
    {
        this._y = y;
    }
    /**
    * Get the x coordinate
    *
    * @method getX
    * @return {Object} x
    */
    getX()
    {
        return this._x;
    }
    /**
    * Get the y coordinate
    *
    * @method getY
    * @return {Object} y
    */
    getY()
    {
        return this._y;
    }
    /**
    * Set the x coordinate
    *
    * @method setDeltaX
    * @param {Object} x
    */
    setDeltaX(x)
    {
        this._deltaX = x;
    }
    /**
    * Set the y delta coordinate
    *
    * @method setDeltaY
    * @param {Object} y
    */
    setDeltaY(y)
    {
        this._deltaY = y;
    }
    /**
    * Get the x coordinate
    *
    * @method getDeltaX
    * @return {Object} x
    */
    getDeltaX()
    {
        return this._deltaX;
    }
    /**
    * Get the y coordinate
    *
    * @method getDeltaY
    * @return {Object} y
    */
    getDeltaY()
    {
        return this._deltaY;
    }
    /**
    * Set the state
    *
    * @method setState
    * @param {Boolean} state
    */
    setState(state)
    {
        this._state = state;
    }
    /**
    * Get the state
    *
    * @method getState
    * return {Boolean} state
    */
    getState()
    {
        return this._state;
    }
    /**
    * Set the type
    *
    * @method setType
    * return {String} type
    */
    setType(type)
    {
        this._type = type;
    }
    /**
    * Get the type
    *
    * @method getType
    * return {String} type
    */
    getType()
    {
        return this._type;
    }
}

/**
 * Fired when a pointer is on (pressed or down)
 * @event on
 */
const EVT_ON = 'on';

/**
 * Fired when a pointer is off (released or erased)
 * @event off
 */
const EVT_OFF = 'off';

/**
 * Fired when a pointer moved
 * @event moved
 */
const EVT_MOVED = 'moved';

class Pointer extends Component
{
    /**
    * Pointer is an abstraction that enables various input devices to send the same kind of events. It is designed to accumulate various type of inputs in a Map of PointerOject. Each input device is converted in a PointerOject in order to unify its interface. Usefully mainly internally for interactive UI objects like buttons.
    *
    * @example
    *    //TODO
    *
    * @class Pointer
    * @constructor
    * @param {Object} params Parameters Object
    * @param {Component} params.components the input components to add to this pointer (i.e. Mouse)
    */
    constructor(params)
    {
        super(params);

        let components = getOrDefault(params, "components", undefined);
        this.pointers = new Map();

        // special pointer to get the last active pointer
        this.lastActivePointer = new PointerObject();
        this.lastActivePointer.setType("lastActive");


        if (Array.isArray(components))
        {
            components.forEach((component) => {
                this.add(component);
            });
        }
    }

    /**
    * Remove unused Pointers
    *
    * @method postUpdate
    */
    postUpdate()
    {
        for(let pointerKey of this.pointers.keys())
        {
            let pointer = this.pointers.get(pointerKey);

            if(pointer.getState() === false && pointer.getType() === TOUCH_TYPE)
            {
                //console.log("erase",pointer);
                this.pointers.delete(pointerKey);
            }

            //update ce qui doit l'Ãªtre mais qui ne peut l'Ãªtre ailleurs
            if(pointer.getType() === "touch")
            {
                pointer.setDeltaX(pointer.getComponent().getDeltaX());
                pointer.setDeltaY(pointer.getComponent().getDeltaY());
            }
            else if(pointer.getType() === "mouse")
            {
                pointer.setDeltaX(pointer.getComponent().getDeltaX());
                pointer.setDeltaY(pointer.getComponent().getDeltaY());
            }

        }
    }

    /**
    * Adds the specified input component as a PointerObject to the pointers list.
    * @method add
    * @param {Component} component
    */
    add(component)
    {
        //console.log("component",component);

        if(component instanceof Touch)
        {
            let touchComponent = component;//just to understand better
            //console.log("will add Touch comp", touchComponent);

            //register a new Pointer at touch creation
            touchComponent.events.on("touchstart", (touch) => {

                let pointer = new PointerObject();
                pointer.setComponent(component);
                pointer.setType(TOUCH_TYPE);
                pointer.setState(true);
                pointer.setX(touch.x);
                pointer.setY(touch.y);
                this.pointers.set(touch.id, pointer);

                this.events.trigger(EVT_ON, {pointer:pointer, x:pointer.getX(), y:pointer.getY()});

                this.lastActivePointer.setState(true);
                this.lastActivePointer.setX(touch.x);
                this.lastActivePointer.setY(touch.y);
                //console.log("touchstart", pointer, this.pointers);
            });

            //updates a Pointer at touch mouved
            touchComponent.events.on("touchmoved", (touch) => {

                let pointer = this.pointers.get(touch.id);
                pointer.setState(true);
                pointer.setX(touch.x);
                pointer.setY(touch.y);
                pointer.setDeltaX(touch.xDelta);
                pointer.setDeltaY(touch.yDelta);
                this.events.trigger(EVT_MOVED, {pointer:pointer, x:pointer.getX(), y:pointer.getY()});

                this.lastActivePointer.setState(true);
                this.lastActivePointer.setX(touch.x);
                this.lastActivePointer.setY(touch.y);
                this.lastActivePointer.setDeltaX(touch.xDelta);
                this.lastActivePointer.setDeltaY(touch.yDelta);
                //console.log("touchmoved", pointer);
            });

            //updates a Pointer at touch mouved
            touchComponent.events.on("touchend", (touch) => {

                let pointer = this.pointers.get(touch.id);
                pointer.setState(false);
                pointer.setX(touch.x);
                pointer.setY(touch.y);

                this.events.trigger(EVT_OFF, {pointer:pointer});

                this.lastActivePointer.setState(false);
                this.lastActivePointer.setX(touch.x);
                this.lastActivePointer.setY(touch.y);
                //console.log("touchend", pointer);
            });

        }
        else if(component instanceof Mouse)
        {
            let mouseComponent = component;//just to understand better

            mouseComponent.events.on("mousepress", () => {
                let pointer = this.pointers.get(component.getID());
                pointer.setState(true);
                this.events.trigger(EVT_ON, {pointer:pointer, x:pointer.getX(), y:pointer.getY()});

                this.lastActivePointer.setState(true);
            });

            mouseComponent.events.on("mouserelease", () => {
                let pointer = this.pointers.get(component.getID());
                pointer.setState(false);
                this.events.trigger(EVT_OFF, {pointer:pointer});

                this.lastActivePointer.setState(false);
            });

            mouseComponent.events.on("mousemove", (coords) => {
                let pointer = this.pointers.get(component.getID());

                pointer.setDeltaX(mouseComponent.getDeltaX());
                pointer.setDeltaY(mouseComponent.getDeltaY());

                pointer.setX(coords.x);
                pointer.setY(coords.y);

                this.events.trigger(EVT_MOVED, {pointer:pointer, x:pointer.getX(), y:pointer.getY()});

                this.lastActivePointer.setState(true);
                this.lastActivePointer.setX(pointer.getX());
                this.lastActivePointer.setY(pointer.getY());
                this.lastActivePointer.setDeltaX(pointer.getDeltaX());
                this.lastActivePointer.setDeltaY(pointer.getDeltaY());
            });

            mouseComponent.events.on("mouseupdate", () => {
                let pointer = this.pointers.get(component.getID());

                pointer.setDeltaX(mouseComponent.getDeltaX());
                pointer.setDeltaY(mouseComponent.getDeltaY());

                /*this.events.trigger(EVT_MOVED, {pointer:pointer, x:pointer.getX(), y:pointer.getY()});*/

                this.lastActivePointer.setState(true);
                this.lastActivePointer.setDeltaX(pointer.getDeltaX());
                this.lastActivePointer.setDeltaY(pointer.getDeltaY());
            });

            let pointer = new PointerObject();
            pointer.setComponent(component);
            pointer.setType(MOUSE_TYPE);

            this.pointers.set(component.getID(), pointer);
        }
    }

    /**
    * Returns the specified PointerObject to work with its state
    * @method get
    * @param {Component} component
    * @return {Component} the corresponding input Component
    */
    get(component)
    {
        return this.pointers.get(component.id);
    }

    /**
    * Returns an Array from the pointers Map object. For debug purpose.
    * @method getPointersArray
    * @return {Array} Pointers array
    */
    getPointers()
    {
        return Array.from(this.pointers.values());
    }

    /**
    * Returns true if ANY of the PointerObjects (input Component) state is true, false otherwise. i.e. if you add a mouse and a touch component to the pointer, this will return true if any of these 2 has it's state to true.
    * @method getState
    * @return {Array} pointers array
    */
    getState()
    {
        for(let pointer of this.pointers.values())
        {
            //the virtual pointer is on true
            if(pointer.getState())
            {
                return true;
            }
        }
        this.events.trigger("off");
        return false;
    }

    /**
    * Returns x coordinates of the PointerObject (input Component) of the given index. If no index is given, the last active pointer will return.
    * @method getX
    * @return {Number} x coordinate
    */
    getX(index)
    {
        let values = [];

        for (let pointer of this.pointers.values())
        {
            values.push(pointer.getX());
        }

        if (index) // we want a specific pointer in the list
        {
            return index === undefined ? values : values[index];
        }

        // we want a value
        return this.lastActivePointer.getX();
    }

    /**
    * Returns y coordinates of the PointerObject (input Component) of the given index. If no index is given, the last active pointer will return.
    * @method getY
    * @return {Number} y coordinate
    */
    getY(index)
    {
        let values = [];

        for (let pointer of this.pointers.values())
        {
            values.push(pointer.getY());
        }

        if (index) // we want a specific pointer in the list
        {
            return index === undefined ? values : values[index];
        }

        // we want a value
        return this.lastActivePointer.getY();
    }

    /**
    * Returns x delta coordinates of the PointerObject (input Component) of the given index. If no index is given, the last active pointer will return.
    * @method getDeltaX
    * @return {Number} coordinate
    */
    getDeltaX(index)
    {
        let values = [];

        for (let pointer of this.pointers.values())
        {
            values.push(pointer.getDeltaX());
        }

        if (index) // we want a specific pointer in the list
        {
            return index === undefined ? values : values[index];
        }

        // we want a value
        return this.lastActivePointer.getDeltaX();
    }

    /**
    * Returns y delta coordinates of the PointerObject (input Component) of the given index. If no index is given, the last active pointer will return.
    * @method getDeltaY
    * @return {Number} coordinate
    */
    getDeltaY(index)
    {
        let values = [];

        for (let pointer of this.pointers.values())
        {
            values.push(pointer.getDeltaY());
        }

        if (index) // we want a specific pointer in the list
        {
            return index === undefined ? values : values[index];
        }

        // we want a value
        return this.lastActivePointer.getDeltaY();
    }
}

// module exports
export { Pointer };
