import * as THREE from 'three';

/**
 * Represents a Quaternion, a way to describe a spatial rotation that is less susceptible to gimbal lock.
 *
 * This class extends the one from Three.js, API available here : http://threejs.org/docs/#Reference/Math/Quaternion
 * @class Quaternion
 */

class Quaternion extends THREE.Quaternion
{
}

/**
@method set
@return Quaternion
@param x {Float}
@param y {Float}
@param z {Float}
@param w {Float}
*/

/**
@method copy
@return Quaternion
@param q {Quaternion}
*/

/**
@method setFromEuler
@return Quaternion
@param euler {Euler}
*/

/**
@method setFromAxisAngle
@return Quaternion
@param axis {Vector3}
@param angle {Float}
*/

/**
@method setFromRotationMatrix
@return Quaternion
@param m {Matrix4}
*/

/**
@method setFromUnitVectors
@return Quaternion
@param vFrom {Vector3}
@param vTo {Vector3}
*/

/**
@method inverse
@return Quaternion
*/

/**
@method length
@return Float
*/

/**
@method normalize
@return Quaternion
*/

/**
@method multiply
@return Quaternion
@param b {Quaternion}
*/

/**
@method multiplyQuaternions
@return Quaternion
@param a {Quaternion}
@param b {Quaternion}
*/

/**
@method multiplyVector3
@return Quaternion
@param vector {Vector3}
@param dest {Vector3}
*/

/**
@method clone
@return Quaternion
*/

/**
 Returns the numerical elements of this quaternion in an array of format (x, y, z, w).
@method toArray
@return Array
@param array {Array}
*/

/**
 Compares each component of *v* to each component of this quaternion to determine if they represent the same rotation.
@method equals
@return Boolean
@param v {Quaternion}
*/

/**
@method lengthSq
@return Float
*/

/**
 Sets this quaternion's component values from an array.
@method fromArray
@return Quaternion
@param array {Array}
*/

/**
@method conjugate
@return Quaternion
*/

/**
 Handles the spherical linear interpolation between quaternions. *t* represents the amount of rotation between this quaternion (where *t* is 0) and quaternionB (where *t* is 1). This quaternion is set to the result. Also see the static version of the *slerp* below.
@method slerp
@return Quaternion
@param quaternionB {Quaternion}
@param t {float}
*/

// module exports
export { Quaternion };
