import {getOrDefault} from '../helpers/Misc';
import {debug} from '../helpers/Debug';
import {Component} from '../Component';

class Timer extends Component
{
    /**
    *A timer is a time manager that gives the possibily to schedule a function every <i>n</i> ms.
    *
    *@class Timer
    *@constructor
    *@param {Object} params Config parameters
    *@param {Number} params.interval the number of millisecond to wait before the next timer's fire
    *@param {Function} params.callback the function to fires every time the timer's interval is passed. Can be an anonymous fonction or not.
    *@example
    *    var myTimer = new Timer({interval: 200, callback: function(){
    *                    //your code here
    *                } });
    *    myTimer.start();
    */
    constructor(params)
    {
        super(params);

        debug.log("constructor post super");

        this.interval = getOrDefault(params, "interval", 100);//milliseconds
        this.callback = getOrDefault(params, "callback", null);//this is a function
    }

    /**
    * Setup the timer with the current time
    *
    * @method setup
    */
    setup(){
        this.startTime = new Date().getTime();
        this.currentTime = new Date().getTime();
    }

    /**
    * Activate the component
    *
    * @method on
    */
    /*on(){
        super.on();

    }*/

    /**
    * Deactivate the component
    *
    * @method off
    */
    off(){
        super.off();

        this.stop();
    }

    /**
    * Starts the timer
    *
    * @method start
    */
    start(){
        if(this._active){
            this.startTime = new Date().getTime();
            this.currentTime = new Date().getTime();
        }else{
            debug.info("component is off");
        }
    }

    /**
    * Starts the timer
    *
    * @method stop
    */
    stop(){
        this.startTime = 0;
        this.currentTime = 0;
    }
    /**
    * Updates the timer's state
    *
    * @method update
    */
    update(){

        if(this._active){

            this.currentTime = new Date().getTime();
            var tempInterval = this.currentTime - this.startTime;

            if(tempInterval >= this.interval){
                this.callback();
                this.reset();
            }
        }

    }

    /**
    * Resets the timer
    * @method reset
    */
    reset(){
        this.startTime = new Date().getTime();
        this.currentTime = new Date().getTime();
    }
}

// module exports
export { Timer };
